<template>
 <div>
     <div
          class="select-list ml-3 mr-3"
        >
          <div class="select-item">
            <p
              class="info-item"
              v-for="(value, key, index) in source.node"
              :key="index"
            >
              <template v-if="key !== 'x' && key !== 'y'">
                <span class="text-hidden" style="width: 40%">{{ key }}</span>
                <span class="text-hidden" style="width: 50%">{{ value }}</span>
              </template>
            </p>
          </div>
    </div>
 </div>
</template>
<script>
export default {
  name: 'infoList',
  props: {
    index: {
      type: Number
    },// 
    source: {
      type: Object,
      default () {
        return {}
      }
    },// 
    label: {
      type: String
    },// 
    value: {
      type: String
    },// 
  },
  mounted(){
  }
}
</script>
<style scoped lang="scss">
.text-hidden {
  display: inline-block;
  white-space: nowrap;
  /* 2.  */
  overflow: hidden;
  /* 3.  */
  text-overflow: ellipsis;
}
.select-list {
  text-align: left;
  border-bottom: 1px solid #eee;
  padding: 10px 0;
}
.info-item {
  display: flex;
  justify-content: space-between;
}
</style>

