<template>
  <v-row style="height: 100%" class="single-cell-graph">
    <v-col
      :cols="drawer ? 9 : 12 / graphNum"
      style="height: 100%"
      v-for="i in graphNum"
      :key="i"
    >
      <v-progress-linear
        v-show="loading"
        indeterminate
        color="yellow darken-2"
      ></v-progress-linear>
      <div class="graph-brain">
        <GraphControls
          :datasetName="datasetName"
          :colorBy="colorBy"
          :filterNum="filterNum"
          :operateActive="operateActive"
          :graphNum="graphNum"
          @changeOperator="changeOperator"
          @openSelectDrawer="openSelectDrawer"
          @separateGraph="separateGraph"
          @deleteSeparateGraph="deleteSeparateGraph" 
          @downloadImage="downloadImage"

        ></GraphControls>
        <v-row style="flex-shrink: 0; flex: 1">
          <v-col cols="12">
            <ScatterBrainSingle
              v-if="datasetType==='single'"
              ref="singleChart"
              :operate="operateActive"
              :typeInfo="typeInfo"
              :colorData="colorData"
              :umapData="partialData"
              :dataRange="dataRange"
              :colorRange="colorRange"
              :total="total"
              :quadTree="quadTree"
              :key="refKey"
              :hoverVal="hoverCategory"
              :nodeMap="nodeMap"
              :sizeData="sizeData"
              :colorBy="colorBy"
              @selectArea="selectArea"
              :selectedTypes="selectedTypes"
              :typeClass="typeClass"
              :typeItem="typeItem"
              :typeValue="typeValue"
              :operatorType="operatorType"
              :filterNum="filterNum"
              :field="field"
              :fieldValue="fieldValue"
              :geneData="geneData"
              :geneExpRange="geneExpRange"
            ></ScatterBrainSingle>
            <ScatterBrainMultiple
              v-else
              ref="multipleChart"
              :operate="operateActive"
              :typeInfo="typeInfo"
              :colorData="colorData"
              :umapData="partialData"
              :dataRange="dataRange"
              :colorRange="colorRange"
              :total="total"
              :quadTree="quadTree"
              :key="refKey"
              :hoverVal="hoverCategory"
              :nodeMap="nodeMap"
              :sizeData="sizeData"
              :colorBy="colorBy"
              @selectArea="selectArea"
              :selectedTypes="selectedTypes"
              :typeClass="typeClass"
              :typeItem="typeItem"
              :typeValue="typeValue"
              :operatorType="operatorType"
              :filterNum="filterNum"
              :field="field"
              :fieldValue="fieldValue"
              :summaryData="summaryData"
            ></ScatterBrainMultiple>
          </v-col>
        </v-row>
      </div>
    </v-col>
    <v-col
      :cols="3"
      style="height: 100vh; border-left: 1px solid #eee"
      v-if="drawer"
    >
      <div class="drawer-title">
        <h3>Selected Cells</h3>
        <v-icon @click="handleCloseDrawer">mdi-close</v-icon>
      </div>
      <v-list style="overflow-y: auto" height="30vh">
        <v-list-item-group v-model="selectedItem" color="primary">
          <v-list-item
            v-for="(item, index) in selectNodes"
            :key="index"
            @click="changeCellItem(index)"
          >
            <v-list-item-avatar>
              <span
                class="color-circle"
                :style="{ backgroundColor: _.get(item, 'key', '#fff') }"
              ></span>
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title>
                {{ _.get(item, ['value', 0, 'node', colorBy], '') }}
              </v-list-item-title>
            </v-list-item-content>

            <v-list-item-action>
              <span>{{ _.get(item, ["value", "length"], "") }}</span>
            </v-list-item-action>
          </v-list-item>
        </v-list-item-group>
      </v-list>
      <div style="border-top: 2px solid #eee; height: 60vh; overflow-y: auto">
        <div class="drawer-title ml-2">
          <div style="display: flex; align-items: center; width: 60%">
            <span
              class="color-circle"
              :style="{ backgroundColor: _.get(selectInfo, 'key', '#fff') }"
            ></span>
            <span class="ml-3 text-hidden" style="width: 50%">{{
              _.get(
                selectInfo,
                ["value", 0, "node", "Siletti_modf_SCANVI_L1"],
                ""
              )
            }}</span>
          </div>
          <v-icon style="flex: 1">mdi-download</v-icon>
        </div>
        <virtual-list
          :data-key="'id'"
          :data-sources="_.get(selectInfo, 'value', [])"
          :data-component="itemComponent"
          :keeps="_.get(selectInfo, ['value', 'length'], 0)"
        />
      </div>
    </v-col>
  </v-row>
</template>

<script>
import ScatterBrainSingle from "./ScatterBrainSingle.vue";
// import ScatterBrainMultiple from "./ScatterBrainMultiple.vue";
import VirtualList from "vue-virtual-scroll-list";
import infoList from "./components/infoList.vue";
import { mapState, mapGetters, mapActions } from 'vuex';
import { nonReactiveDataMixin } from '@/utils/nonReactiveDataStore';
import GraphControls from "../components/visualization/GraphControls.vue";

export default {
  mixins: [nonReactiveDataMixin], //  mixin
  props: {
    // 
    datasetType: {
      type: String,
      default: 'single'
    },
    // 
    visualizationType: {
      type: String,
      default: 'umap'
    }
  },
  components: {
    VirtualList,
    // ScatterBrainMultiple,
    ScatterBrainSingle,
    GraphControls,
  },
  data() {
    return {
      itemComponent: infoList,
      graphNum: 1,
      selectedItem: 0,
      selectInfo: {},
      colorData: [],
      orginColorData: [],
      refKey: 0,
      umapData: [],
      partialData: [],
      colorRange: {},
      sizeData: [],
      quadTree: null,
      nodeMap: null,
      hoverCategory: -1,
      treeMap: new Map(),
      typeClass: [],
      typeItem: [],
      typeValue: [],
      operatorType: [],
      geneData: [],
      selectedTypes: [] //  selectedTypes 
    };
  },
  computed: {
    //  computedgettersnonReactiveDataStore
    ...mapState('cellData', [
      'typeInfo',
      'classInfo',
      // 'geneExp',      // nonReactiveDataStore
      'hoverInfo',
      'field',
      // 'fieldValue',   // nonReactiveDataStore
      'total',
      'dataRange',
      'summaryData',
      'colorField',
      'geneExpRange'
    ]),
    ...mapGetters('cellData', [
      'getGeneExp',
      'getFieldValue'
    ]),
    ...mapState('uiState', [
      'drawer',
      'operateActive',
      'selectNodes'
    ]),
    ...mapGetters('uiState', [
      'isDrawerOpen'
    ]),
    
    // gettersnonReactiveDataStore
    geneExp() {
      return this.getGeneExp;
    },
    fieldValue() {
      return this.getFieldValue;
    },
    
    colorBy() {
      return this.colorField;
    },
    datasetName() {
      return this.$store.state.cellData.datasetName;
    },
    filterNum() {
      return this.typeClass.length;
    },
    ...mapState({
      loading: state => state.loading
    }),
    // propsdatasetType
    // datasetType() {
    //   return this.datasetType;
    // }
  },
  watch: {
    drawer: {
      handler(newDrawerState, oldDrawerState) {
        this.$nextTick(() => {
          setTimeout(() => {
            this.handleDrawerStateChange(newDrawerState);
          }, 100);
        });
      },
      immediate: false
    },
    typeInfo: {
      handler(val) {
        this.filterTypeData(val);
      },
      deep: true
    },
    classInfo: {
      handler(val) {
        this.filterTypeData(this.typeInfo);
      },
      deep: true,
    },
    geneExp: {
      handler(val) {
        if (_.isEmpty(val)) {
          this.geneData = [];
        } else {
          this.filterGeneExp(val);
        }
      },
      deep: true,
    },
    hoverInfo: {
      handler(val) {
        if (!_.isEmpty(val)) {
          this.getHoverFilterData(val);
        } else {
          this.hoverCategory = -1;
          this.filterTypeData(this.typeInfo);
        }
      },
      deep: true,
    },
  },
  methods: {
    ...mapActions('uiState', [
      'setOperateActive',
      'toggleDrawer',
      'closeDrawer',
      'openDrawer',
      'setSelectNodes'
    ]),
    filterTypeData(typeInfo) {
      const typeClass = [];
      const typeItem = [];
      const typeValue = [];
      const operateActive = [];
      let filterNum = 0;
      let flag = false;
      
      for (const key in typeInfo) {
        if (key !== "Class") {
          // 
          const types = typeInfo[key];
          const index = _.indexOf(this.field, key);
          
          // 
          if (index === -1) continue;
          
          const classValue = Math.floor(index / 4);
          const itemValue = index % 4;
          
          for (const type of types) {
            // 
            let exitOther = false;
            for (let i = 0; i < filterNum; i++) {
              if (typeClass[i] === classValue && typeItem[i] === itemValue) {
                if (!flag && operateActive[i] === 0) {
                  flag = true;
                } else {
                  exitOther = true;
                }
              }
            }
            
            typeClass.push(classValue);
            typeItem.push(itemValue);
            
            const valueIndex = _.indexOf(this.fieldValue[key], type);
            typeValue.push(valueIndex);
            
            // 
            if (flag) {
              operateActive.push(2); // OR
            } else if (exitOther || operateActive.length === 0) {
              operateActive.push(1);
            } else {
              operateActive.push(0); // AND
            }
            
            filterNum++;
          }
        } else {
          // Class
          // classData
          const classData = typeInfo[key];
          
          // classData
          if (Array.isArray(classData)) {
            // 
            const index = _.indexOf(this.field, "Siletti_modf_SCANVI_L2");
            
            // 
            if (index === -1) continue;
            
            const classPositionValue = Math.floor(index / 4);
            const itemPositionValue = index % 4;
            
            // 
            for (const childNode of classData) {
              typeClass.push(classPositionValue);
              typeItem.push(itemPositionValue);
              
              const valueIndex = _.indexOf(this.fieldValue["Siletti_modf_SCANVI_L2"], childNode);
              if (valueIndex !== -1) { // 
                typeValue.push(valueIndex);
                operateActive.push(1); // OR
                filterNum++;
              }
            }
          } else {
            // 
            for (const classKey in classData) {
              const lv2Value = classData[classKey];
              const index = _.indexOf(this.field, "Siletti_modf_SCANVI_L2");
              
              // 
              if (index === -1) continue;
              
              const classPositionValue = Math.floor(index / 4);
              const itemPositionValue = index % 4;
              
              for (const lv2 of lv2Value) {
                typeClass.push(classPositionValue);
                typeItem.push(itemPositionValue);
                
                const valueIndex = _.indexOf(this.fieldValue["Siletti_modf_SCANVI_L2"], lv2);
                typeValue.push(valueIndex);
                
                operateActive.push(1); // OR
                filterNum++;
              }
            }
          }
        }
      }

      this.typeClass = typeClass;
      this.typeItem = typeItem;
      this.typeValue = typeValue;
      this.operatorType = operateActive;
      
      // storeScatterBrainSingle
      this.$store.commit('uiState/SET_FILTER_DATA', {
        typeClass,
        typeItem,
        typeValue,
        operatorType: operateActive,
        filterNum
      });
    },
    filterGeneExp(geneExp) {
      let keys = Object.keys(geneExp);
      let key = keys[0];
      this.geneData = geneExp[key];
    },
    separateGraph() {
      this.setOperateActive(3);
      this.graphNum = 2;
      this.colorData = this.orginColorData;
      this.partialData = this.umapData;
      this.refKey = this.refKey + 1;
    },
    deleteSeparateGraph() {
      if (this.graphNum > 1) {
        this.graphNum = 1;
        this.setOperateActive(1);
      }
    },
    selectArea(selected) {
      if (!selected || !selected.length) return;
      
      const selectNodes = [];
      
      // 
      for (let i = 0; i < selected.length; i++) {
        const node = {};
        let currentInfo = selected[i].slice(2, selected[i].length-1) || [];
        
        // 
        currentInfo.forEach((item, index) => {
          if (index < selected[i].length - 1) {
            node[this.field[index]] = this.fieldValue[this.field[index]][item];
          }
        });
        
        // 
        const colors = selected[i][selected[i].length - 1] || [];
        let color = colors.map(item => Math.round(item * 255)).join(",");
        color = `rgba(${color})`;
        
        selectNodes.push({ node, color, id: i });
      }
      
      // 
      const groupNodes = _.groupBy(selectNodes, "color");
      const selectGroupNodes = Object.keys(groupNodes).map(key => ({ 
        key, 
        value: groupNodes[key] 
      }));
      
      this.setSelectNodes(selectGroupNodes);
      this.selectInfo = selectGroupNodes[0] || {};
      this.colorData = this.orginColorData;
      this.partialData = this.umapData;
    },
    changeCellItem(index = 0) {
      this.selectInfo = this.selectNodes[index];
    },
    changeOperator(index) {
      this.setOperateActive(index);
      if (index === 1) {
        // arrow-expand-all
        this.clearSelectionAndRestore();
        
        // 
        if (this.drawer) {
          this.closeDrawer();
        }
      }
    },
    openSelectDrawer() {
      this.openDrawer();
      this.setOperateActive(2);
    },
    getHoverFilterData(hoverInfo) {
      if (!hoverInfo || !hoverInfo.key) return;
      
      const index = _.indexOf(this.field, hoverInfo.key);
      if (index === -1) return;
      
      const classValue = Math.floor(index / 4);
      const itemValue = index % 4;
      const valueIndex = _.indexOf(this.fieldValue[hoverInfo.key], hoverInfo.value);
      
      if (valueIndex === -1) return;
      
      // 
      for (let i = 0; i < this.filterNum; i++) {
        if (this.typeClass[i] === classValue && 
            this.typeItem[i] === itemValue && 
            this.typeValue[i] === valueIndex) {
          return; // 
        }
      }
      
      // 
      this.typeClass.push(classValue);
      this.typeItem.push(itemValue);
      this.typeValue.push(valueIndex);
      this.operatorType.push(1); // OR
      this.hoverCategory = valueIndex;
    },
    downloadImage() {
      const chartComponent = this.datasetType === 'single' 
        ? this.$refs.singleChart 
        : this.$refs.multipleChart;
      
      if (!chartComponent) {
        this.directDownload();
        return;
      }
      
      if (typeof chartComponent.downloadImage !== 'function') {
        if (this.datasetType === 'single' && chartComponent.backupDownload) {
          chartComponent.backupDownload();
          return;
        }
        
        this.directDownload();
        return;
      }
      
      try {
        chartComponent.downloadImage();
      } catch (error) {
        this.directDownload();
      }
    },
    
    directDownload() {
      try {
        const container = this.datasetType === 'single' 
          ? document.querySelector('#canvasContainer')
          : document.querySelector('.scatter-container');
        
        if (!container) return;
        
        const canvas = container.querySelector('canvas');
        if (!canvas) return;
        
        const link = document.createElement('a');
        const fileName = `scatter_plot_${this.colorBy || 'default'}.png`;
        link.download = fileName;
        link.href = canvas.toDataURL('image/png');
        
        document.body.appendChild(link);
        link.click();
        
        setTimeout(() => {
          document.body.removeChild(link);
          URL.revokeObjectURL(link.href);
        }, 100);
      } catch (error) {
        console.error(':', error);
      }
    },
    handleDrawerStateChange(newDrawerState) {
      console.log(`: ${newDrawerState ? '' : ''}`);
      
      // 
      if (!newDrawerState) {
        console.log('');
        try {
          // 
          this.setOperateActive(1);
          
          // 
          this.setSelectNodes([]);
          this.selectInfo = {};
          
          // 
          const chartComponent = this.datasetType === 'single' 
            ? this.$refs.singleChart 
            : this.$refs.multipleChart;
          
          if (chartComponent) {
            // 
            if (typeof chartComponent.clearSelection === 'function') {
              chartComponent.clearSelection();
            }
            
            // 
            if (typeof chartComponent.setMode === 'function') {
              chartComponent.setMode('pan');
            }
            
            // ScatterBrainSingle
            if (this.datasetType === 'single' && chartComponent.renderer) {
              if (typeof chartComponent.renderer.clearSelection === 'function') {
                chartComponent.renderer.clearSelection();
              }
              if (typeof chartComponent.renderer.setMode === 'function') {
                chartComponent.renderer.setMode('pan');
              }
            }
          }
          
          // 
          this.colorData = this.orginColorData;
          this.partialData = this.umapData;
          
          console.log('');
        } catch (error) {
          console.error(':', error);
        }
      }
      
      // 
      if (this.datasetType === 'single' && this.$refs.singleChart) {
        // 
        if (this.$refs.singleChart.handleContainerResize) {
          this.$refs.singleChart.handleContainerResize();
        }
      } else if (this.datasetType === 'multiple' && this.$refs.multipleChart) {
        // 
        if (this.$refs.multipleChart.handleContainerResize) {
          this.$refs.multipleChart.handleContainerResize();
        }
      }
      
      // 
      if (this.graphNum > 1) {
        // 
        const chartRefs = this.$refs.singleChart || this.$refs.multipleChart;
        if (Array.isArray(chartRefs)) {
          chartRefs.forEach(chart => {
            if (chart && chart.handleContainerResize) {
              chart.handleContainerResize();
            }
          });
        }
      }
    },
    /**
     * 
     */
    clearSelectionAndRestore() {
      console.log('');
      
      try {
        // 1. 
        this.setOperateActive(1);
        
        // 2. 
        this.setSelectNodes([]);
        this.selectInfo = {};
        
        // 3. 
        const chartComponent = this.datasetType === 'single' 
          ? this.$refs.singleChart 
          : this.$refs.multipleChart;
        
        if (chartComponent) {
          // 
          if (typeof chartComponent.clearSelection === 'function') {
            chartComponent.clearSelection();
          }
          
          // 
          if (typeof chartComponent.setMode === 'function') {
            chartComponent.setMode('pan');
          }
          
          // ScatterBrainSingle
          if (this.datasetType === 'single' && chartComponent.renderer) {
            if (typeof chartComponent.renderer.clearSelection === 'function') {
              chartComponent.renderer.clearSelection();
            }
            if (typeof chartComponent.renderer.setMode === 'function') {
              chartComponent.renderer.setMode('pan');
            }
          }
        }
        
        // 4. 
        this.colorData = this.orginColorData;
        this.partialData = this.umapData;
        
        console.log('');
      } catch (error) {
        console.error(':', error);
      }
    },
    /**
     * 
     * 
     */
    handleCloseDrawer() {
      console.log('Selected Cells');
      
      try {
        // 
        this.clearSelectionAndRestore();
        
        // watch
        this.closeDrawer();
        
        console.log('');
      } catch (error) {
        console.error(':', error);
      }
    },
    

  },
};
</script>

<style lang="scss" scoped>
.v-icon__svg {
  width: 1.25rem;
  height: 1.25rem;
  fill: #000;
}
.graph-icon {
  width: 2.5rem;
  height: 2.5rem;
  cursor: pointer;
  &:hover {
    background-color: #eee;
  }
}
.opearate_active {
  background-color: #eee;
}
.color-circle {
  flex-shrink: 0;
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.drawer-title {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
.info-item {
  display: flex;
  justify-content: space-between;
}
.text-hidden {
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.select-list {
  text-align: left;
  border-bottom: 1px solid #eee;
  padding: 10px 0;
}
.graph-brain {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.single-cell-graph {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
</style>
