<template>
  <div class="tab-panel">
    <div class="vm-header br cell-row1">
      <p>
        <span class="color-block"></span>
        <span class="cell-row1-text">SELECTED VIEW</span>
      </p>
      <p style="font-size: 20px; margin: 10px 0">
        {{datasetName}}
        <v-icon dense class="icon-set small-icon"
          >mdi-information-outline</v-icon
        >
      </p>
      <p>{{datasetName}}</p>
    </div>
    <div
      class="vm-header br cell-row1 cell-row2"
      style="background-color: #fff"
    >
      <p style="font-size: 20px">
        <span style="margin-right: 5px">Genes</span>
      </p>
      <p style="color: #666;margin-bottom:15px !important;">{{geneList.length}} in dataset</p>
      <div>
        <v-autocomplete
          v-if="!showBatchInput"
          v-model="geneSelected"
          :items="geneList"
          outlined
          chips
          clearable
          label="Search by gene symbol"
          item-text="label"
          item-value="value"
          multiple
          dense
          @change="selectGene"
        >
          <template v-slot:selection="data">
            <v-chip
              v-bind="data.attrs"
              :input-value="data.selected"
              close
              label
              outlined
              small
              @click="data.select"
              @click:close="remove(data.item)"
            >
              {{ data.item.label}}
            </v-chip>
          </template>
          <template v-slot:item="data">
            <template>
              <v-list-item-content
                >{{ data.item.label }}</v-list-item-content>
            </template>
          </template>
        </v-autocomplete>
        <v-textarea
          v-else
          v-model="batchInput"
          outlined
          dense
          auto-grow
          rows="3"
          label="Enter gene symbols separated by commas"
          placeholder="e.g. Gad1, Slc17a7, Aqp4"
          @keydown.esc="showBatchInput = false"
        ></v-textarea>
        <div class="gene-symbol-btn">
          <v-btn class="ma-2" outlined color="indigo" @click="clearGenes"> CLEAR</v-btn>
          <v-btn v-if="!showBatchInput" class="ma-2" outlined color="indigo" @click="showBatchInput = true"> ADD BATCH </v-btn>
          <v-btn v-else class="ma-2" outlined color="indigo" @click="addBatchGenes"> SUBMIT </v-btn>
          <v-btn v-if="showBatchInput" class="ma-2" outlined color="error" @click="showBatchInput = false"> CANCEL </v-btn>
        </div>
      </div>
    </div>
    <div style="display: flex">
      <v-expansion-panels class="mb-6" accordion>
        <v-expansion-panel v-for="(item, i) in geneSelected" :key="i">
          <v-expansion-panel-header
            hide-actions
            style="display: flex; justify-content: space-between"
            @click="handlePanelClick(item)"
          >
            <template v-slot:default="{ open }">
              <v-row no-gutters justify="space-between">
                <v-col
                  cols="4"
                  style="text-align: left; display: flex; align-items: center"
                >
                  <v-icon v-if="!open">mdi-chevron-right</v-icon>
                  <v-icon v-else>mdi-chevron-down</v-icon> {{item}}
                </v-col>
                <v-col cols="4" class="gene-symbol-icon">
                  <v-icon style="font-size:20px;" class="icon-set icon10">mdi-cog</v-icon>
                  <span @click.stop="handleColorIconClick(item)" 
                        class="icon-set icon10 color-icon" 
                        :class="{ 'active-gene': activeGeneForRendering === item }"
                        :title="activeGeneForRendering === item ? '' : ''">
                    <svg
                      viewBox="0 0 24 24"
                      role="img"
                      aria-hidden="true"
                      class="v-icon__svg"
                    >
                      <path
                        d="M12,19.58V19.58C10.4,19.58 8.89,18.96 7.76,17.83C6.62,16.69 6,15.19 6,13.58C6,12 6.62,10.47 7.76,9.34L12,5.1M17.66,7.93L12,2.27V2.27L6.34,7.93C3.22,11.05 3.22,16.12 6.34,19.24C7.9,20.8 9.95,21.58 12,21.58C14.05,21.58 16.1,20.8 17.66,19.24C20.78,16.12 20.78,11.05 17.66,7.93Z"
                      ></path>
                    </svg>
                  </span>
                  <v-icon style="font-size:20px;" class="icon-set icon10" @click.stop="removeItem(item)">mdi-window-close</v-icon>
                </v-col>
              </v-row>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <div class="gene-expression-panel">
              <div class="gene-expression-stats">
                <div class="expression-histogram">
                  <div class="histogram-header">
                    <span v-if="selectedRange[item]" class="selection-range">
                      Selection Range: {{ formatExpression(selectedRange[item].start) }} - {{ formatExpression(selectedRange[item].end) }}
                      <span class="selection-stats" v-if="getSelectionStats(item)">
                        ({{ getSelectionStats(item).count }} cells, {{ getSelectionStats(item).percentage }}%)
                      </span>
                    </span>
                    <span class="expression-percentage">{{ getExpressionPercentage(item) }}%</span>
                  </div>
                  
                  <div class="histogram-container" ref="histogramContainer">
                    <svg class="histogram-svg" :id="`histogram-${item}`" 
                         @mousedown="startSelection($event, item)" 
                         @mousemove="updateSelection($event, item)"
                         @mouseup="endSelection(item)"
                         @mouseleave="cancelSelection"
                         ref="histogramSvg"
                         preserveAspectRatio="none">
                      <g class="axis x-axis">
                        <text x="2%" y="120">{{ getExpressionRange(item).min.toFixed(1) }}</text>
                        <text x="25%" y="120">{{ getAxisTickValue(item, 0.25).toFixed(1) }}</text>
                        <text x="50%" y="120">{{ getAxisTickValue(item, 0.5).toFixed(1) }}</text>
                        <text x="75%" y="120">{{ getAxisTickValue(item, 0.75).toFixed(1) }}</text>
                        <text x="98%" y="120" text-anchor="end">{{ getExpressionRange(item).max.toFixed(1) }}</text>
                      </g>
                      <!--  -->
                      <g class="bars" v-if="currentSelectedGeneInfo[item]" :key="`bars-${item}-${Object.keys(currentSelectedGeneInfo).length}`">
                        <rect v-for="(bar, index) in generateHistogramData(item)" 
                              :key="`${item}-bar-${index}-${bar.count}`"
                              :x="`${4 + (index * 92 / 50)}%`"
                              :y="100 - bar.height"
                              :width="`${92 / 50}%`"
                              :height="bar.height"
                              :fill="getBarColor(bar, item, index)"
                              :stroke="isInSelectionRange(bar, item) ? '#0D47A1' : 'none'"
                              :stroke-width="isInSelectionRange(bar, item) ? 0.5 : 0"
                              ></rect>
                      </g>
                      
                      <!--  -->
                      <rect v-if="selectionActive && currentSelectionGene === item"
                            :x="selectionStart.x"
                            :y="0"
                            :width="getSelectionWidth(item)"
                            :height="100"
                            fill="rgba(66, 133, 244, 0.2)"
                            stroke="#4285F4"
                            stroke-width="1"></rect>
                      
                      <!--  -->
                      <rect v-if="!selectionActive && selectedRange[item] && !isNaN(getSelectionRangePosition(item).start) && !isNaN(getSelectionRangePosition(item).width)"
                            :x="getSelectionRangePosition(item).start"
                            :y="0"
                            :width="getSelectionRangePosition(item).width"
                            :height="100"
                            fill="rgba(66, 133, 244, 0.2)"
                            stroke="#4285F4"
                            stroke-width="1"></rect>
                    </svg>
                  </div>
                </div>
                
                <div class="gene-info">
                  <div class="gene-avg-expression">{{ getAverageExpression(item) }}</div>
                  <v-btn v-if="selectedRange[item]" x-small text color="error" class="ml-2" @click="clearSelection(item)">
                    <v-icon x-small left>mdi-close</v-icon>Clear Selection
                  </v-btn>
                </div>
              </div>
              
              <div class="expression-controls">
                <v-btn small icon color="primary" class="download-btn">
                  <v-icon small>mdi-download</v-icon>
                </v-btn>
              </div>
            </div>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex';
import { nonReactiveDataMixin } from '@/utils/nonReactiveDataStore';
import { buildDatasetPath, getDataset } from '@/api/config';

export default {
  name: "Genes",
  mixins: [nonReactiveDataMixin], //  mixin
  data() {
    return {
      showBatchInput: false,
      batchInput: "",
      geneSelected: [],
      histogramData: {},
      selectionActive: false,
      selectionStart: { x: 0, y: 0 },
      selectionEnd: { x: 0, y: 0 },
      currentSelectionGene: null,
      selectedRange: {},
      currentSelectedGeneInfo: {},
      isUpdating: false, // 
      name: "Midnight Crew",
      title: "The summer breeze",
      geneTotal: 0,
      geneData: null,
      chunkCache: {}, // chunk
      selectionStats: {}, // 
      currentOpenPanel: null, // 
      geneColor: '',
      activeGeneForRendering: null, // 
    };
  },
  computed: {
    //  computedgettersnonReactiveDataStore
    ...mapState('cellData', [
      'geneList',
      'datasetName',
      // 'geneExp',      // nonReactiveDataStore
      'geneExpRange'
    ]),
    ...mapGetters('cellData', [
      'getGeneExp'
    ]),
    
    // gettersnonReactiveDataStore
    geneExp() {
      return this.getGeneExp;
    }
  },
  async mounted() {
    // 
    if (typeof window !== 'undefined') {
      window.addEventListener('datasetChanged', this.handleDatasetChange);
    }
    
    // 
    await this.loadGeneList();

    // 
    await this.loadMatrixInfo();
    
    // 
    this.loadSavedRanges();
  },
  
  beforeDestroy() {
    if (typeof window !== 'undefined') {
      window.removeEventListener('datasetChanged', this.handleDatasetChange);
    }
  },
  
  methods: {
    ...mapActions('cellData', [
      'loadGeneExpression',
      'loadGeneList'
    ]),
    
    /**
     * 
     */
    async handleDatasetChange(event) {
      console.log('Genes: ', event.detail.dataset);
      
      try {
        //  
        this.chunkCache = {};
        this.currentSelectedGeneInfo = {};
        this.selectedRange = {};
        this.selectionStats = {};
        this.activeGeneForRendering = null;
        this.geneSelected = [];
        this.geneData = null;
        this.geneTotal = 0;
        
        console.log('Genes: ');
        
        //  
        console.log('Genes: ...');
        await this.loadGeneList();
        
        //  
        console.log('Genes: ...');
        await this.loadMatrixInfo();
        
        //  
        this.$emit("getGeneExp", {});
        this.$emit("currentRangeSelected", {
          gene: null,
          range: null,
          stats: null,
          geneData: null
        });
        
        console.log('Genes: ');
        
      } catch (error) {
        console.error('Genes: :', error);
      }
    },
    
    /**
     * 
     */
    async loadMatrixInfo() {
      try {
        console.log('Genes: ...');
        const matrixInfoPath = buildDatasetPath('matrix_info.json');
        console.log('Genes: :', matrixInfoPath);
        
        let geneInfo = await fetch(matrixInfoPath);
        if (!geneInfo.ok) {
          throw new Error(`HTTP ${geneInfo.status}: ${geneInfo.statusText}`);
        }
        
        this.geneData = await geneInfo.json();
        this.geneTotal = this.geneData.total_genes || (this.geneList ? this.geneList.length : 0);
        
        console.log('Genes: ');
        console.log('Genes: :', this.geneTotal);
        console.log('Genes: :', this.geneData.total_cells);
        console.log('Genes: chunk:', this.geneData.genes_per_chunk);
        
      } catch (error) {
        console.error('Genes: :', error);
        this.geneTotal = this.geneList ? this.geneList.length : 0;
        
        // geneData
        this.geneData = {
          total_genes: this.geneTotal,
          total_cells: 0,
          genes_per_chunk: 100 // 
        };
      }
    },
    clearGenes() {
      console.log('');
      this.geneSelected = [];
      this.currentSelectedGeneInfo = {};
      this.selectedRange = {};
      this.selectionStats = {};
      this.activeGeneForRendering = null; // 
      
      // 
      this.$emit("getGeneExp", {});
      
      // 
      this.$emit("currentRangeSelected", {
        gene: null,
        range: null,
        stats: null,
        geneData: null
      });
    },
    addBatchGenes() {
      if (!this.batchInput.trim()) {
        this.showBatchInput = false;
        return;
      }
      
      // 
      const geneNames = this.batchInput.split(',').map(name => name.trim()).filter(name => name);
      
      // 
      const validGenes = [];
      for (const geneName of geneNames) {
        const found = this.geneList.find(gene => gene.value.toLowerCase() === geneName.toLowerCase());
        if (found && !this.geneSelected.includes(found.value)) {
          validGenes.push(found.value);
        }
      }
      
      // 
      if (validGenes.length > 0) {
        this.geneSelected = [...this.geneSelected, ...validGenes];
        //  selectGene() Vue  @change 
        // this.selectGene(); // 
      }
      
      // 
      this.batchInput = "";
      this.showBatchInput = false;
    },
    remove(item) {
      const index = this.geneSelected.indexOf(item.value);
      if (index >= 0) {
        const geneName = this.geneSelected[index];
        
        // 
        delete this.currentSelectedGeneInfo[geneName];
        // 
        delete this.selectedRange[geneName];
        delete this.selectionStats[geneName];
        
        // 
        if (this.activeGeneForRendering === geneName) {
          // 
          const remainingGenes = this.geneSelected.filter(gene => gene !== geneName);
          this.activeGeneForRendering = remainingGenes.length > 0 ? remainingGenes[0] : null;
          
          console.log(` ${geneName}: ${this.activeGeneForRendering}`);
        }
        
        this.geneSelected.splice(index, 1);
        
        // 
        if (this.geneSelected.length === 0) {
          // 
          console.log('');
          this.activeGeneForRendering = null;
          this.currentSelectedGeneInfo = {};
          this.selectedRange = {};
          this.selectionStats = {};
          
          // 
          this.$emit("getGeneExp", {});
          
          // 
          this.$emit("currentRangeSelected", {
            gene: null,
            range: null,
            stats: null,
            geneData: null
          });
        } else {
          // 
          if (this.activeGeneForRendering && this.currentSelectedGeneInfo[this.activeGeneForRendering]) {
            const activeGeneData = {
              [this.activeGeneForRendering]: this.currentSelectedGeneInfo[this.activeGeneForRendering]
            };
            this.$emit("getGeneExp", activeGeneData);
        
            // 
            if (this.selectedRange[this.activeGeneForRendering] && this.selectionStats[this.activeGeneForRendering]) {
              this.$emit("currentRangeSelected", {
                gene: this.activeGeneForRendering,
                range: this.selectedRange[this.activeGeneForRendering],
                stats: this.selectionStats[this.activeGeneForRendering],
                geneData: this.currentSelectedGeneInfo[this.activeGeneForRendering],
                color: true
              });
            }
          } else {
            this.$emit("getGeneExp", {});
          }
        }
      }
    },
    removeItem(item) {
      // 
      const index = this.geneSelected.indexOf(item);
      if (index >= 0) {
        // 
        delete this.currentSelectedGeneInfo[item];
        // 
        delete this.selectedRange[item];
        delete this.selectionStats[item];
        
        // 
        if (this.activeGeneForRendering === item) {
          // 
          const remainingGenes = this.geneSelected.filter(gene => gene !== item);
          this.activeGeneForRendering = remainingGenes.length > 0 ? remainingGenes[0] : null;
          
          console.log(` ${item}: ${this.activeGeneForRendering}`);
        }
        
        this.geneSelected.splice(index, 1);
        
        // 
        if (this.geneSelected.length === 0) {
          // 
          console.log('');
          this.activeGeneForRendering = null;
          this.currentSelectedGeneInfo = {};
          this.selectedRange = {};
          this.selectionStats = {};
          
          // 
          this.$emit("getGeneExp", {});
          
          // 
          this.$emit("currentRangeSelected", {
            gene: null,
            range: null,
            stats: null,
            geneData: null
          });
        } else {
          // 
        if (this.activeGeneForRendering && this.currentSelectedGeneInfo[this.activeGeneForRendering]) {
          const activeGeneData = {
            [this.activeGeneForRendering]: this.currentSelectedGeneInfo[this.activeGeneForRendering]
          };
          this.$emit("getGeneExp", activeGeneData);
            
            // 
            if (this.selectedRange[this.activeGeneForRendering] && this.selectionStats[this.activeGeneForRendering]) {
              this.$emit("currentRangeSelected", {
                gene: this.activeGeneForRendering,
                range: this.selectedRange[this.activeGeneForRendering],
                stats: this.selectionStats[this.activeGeneForRendering],
                geneData: this.currentSelectedGeneInfo[this.activeGeneForRendering],
                color: true
              });
            }
        } else {
          this.$emit("getGeneExp", {});
        }
        }
      }
    },
    async selectGene(val){
      // 
      if (this.isUpdating) {
        console.log('selectGene: ');
        return;
      }
      
      // 
      if (!this.geneSelected || this.geneSelected.length === 0) {
        console.log('selectGene: ');
        this.currentSelectedGeneInfo = {};
        this.selectedRange = {};
        this.selectionStats = {};
        this.activeGeneForRendering = null;
        this.$emit("getGeneExp", {});
        this.$emit("currentRangeSelected", {
          gene: null,
          range: null,
          stats: null,
          geneData: null
        });
        return;
      }
      
      // 
      this.isUpdating = true;
      
      try {
        console.log(` ${this.geneSelected.length} `);
        
        let selectedGeneInfo = {};
        
        // chunk
        const chunksToLoad = new Set();
        const geneIndexMap = new Map();
        
        for(let i = 0; i < this.geneSelected.length; i++) {
          const geneName = this.geneSelected[i];
          const index = this.geneList.findIndex(item => item.value === geneName);
          
          if (index === -1) {
            console.warn(` ${geneName} `);
            continue;
          }
          
          //  geneData 
          if (!this.geneData || !this.geneData.genes_per_chunk) {
            console.error('geneData chunk');
            continue;
          }
          
          const chunkIndex = Math.floor(index / this.geneData.genes_per_chunk);
          const geneIndex = index % this.geneData.genes_per_chunk;
          
          console.log(` ${geneName}: =${index}, chunk=${chunkIndex}, geneIndex=${geneIndex}`);
          
          chunksToLoad.add(chunkIndex);
          geneIndexMap.set(geneName, {chunkIndex, geneIndex});
        }
        
        // chunk
        const loadPromises = [];
        for(const chunkIndex of chunksToLoad) {
          if(!this.chunkCache[chunkIndex]) {
            console.log(`chunk ${chunkIndex}`);
            loadPromises.push(this.loadChunkData(chunkIndex));
          } else {
            console.log(`chunk ${chunkIndex} `);
          }
        }
        
        if(loadPromises.length > 0) {
          console.log(` ${loadPromises.length} chunk`);
          await Promise.all(loadPromises);
        }
        
        // 
        for(let i = 0; i < this.geneSelected.length; i++) {
          const geneName = this.geneSelected[i];
          const geneInfo = geneIndexMap.get(geneName);
          
          if (!geneInfo) {
            console.warn(` ${geneName} `);
            continue;
          }
          
          const {chunkIndex, geneIndex} = geneInfo;
          
          if (!this.chunkCache[chunkIndex] || !this.chunkCache[chunkIndex][geneIndex]) {
            console.warn(` ${geneName}  (chunk: ${chunkIndex}, index: ${geneIndex})`);
            continue;
          }
          
          const geneData = this.chunkCache[chunkIndex][geneIndex];
          selectedGeneInfo[geneName] = geneData;
          
          // 
          console.log(` ${geneName} : =${geneData.length}, 5=[${geneData.slice(0, 5).join(', ')}]`);
          
          // 
          const nonZeroCount = geneData.filter(val => val > 0).length;
          const maxVal = Math.max(...geneData);
          const minVal = Math.min(...geneData.filter(val => val > 0));
          console.log(` ${geneName} : =${nonZeroCount}, =${maxVal.toFixed(3)}, =${minVal.toFixed(3)}`);
        }
        
        this.currentSelectedGeneInfo = selectedGeneInfo;
        console.log(':', Object.keys(this.currentSelectedGeneInfo));
        
        // 
        this.validateGeneDataDifferences();
        
        // 
        if (!this.activeGeneForRendering && this.geneSelected.length > 0) {
          this.activeGeneForRendering = this.geneSelected[0];
          console.log(` ${this.activeGeneForRendering} `);
        }
        
        // 
        if (this.activeGeneForRendering && selectedGeneInfo[this.activeGeneForRendering]) {
          const activeGeneData = {
            [this.activeGeneForRendering]: selectedGeneInfo[this.activeGeneForRendering]
          };
          this.$emit("getGeneExp", activeGeneData);
        } else {
          // 
          this.$emit("getGeneExp", this.currentSelectedGeneInfo);
        }
        
        // 
        for(const geneName of this.geneSelected) {
          if (!this.selectedRange[geneName]) {
            const range = this.getExpressionRange(geneName);
            this.selectedRange[geneName] = {
              start: range.min,
              end: range.max
            };
            
            // 
            this.calculateSelectionStats(geneName);
          }
        }
        
        // 
        this.saveSelectedRanges();
        
        // 
        if (this.activeGeneForRendering && this.selectedRange[this.activeGeneForRendering]) {
          console.log(` ${this.activeGeneForRendering} `);
          this.$emit("currentRangeSelected", {
            gene: this.activeGeneForRendering,
            range: this.selectedRange[this.activeGeneForRendering],
            stats: this.selectionStats[this.activeGeneForRendering],
            geneData: selectedGeneInfo[this.activeGeneForRendering],
            color: true // 
          });
        }
        
        // 
        this.$nextTick(() => {
          this.$forceUpdate();
          console.log('');
        });
        
      } catch (error) {
        console.error('selectGene :', error);
      } finally {
        // 
        this.isUpdating = false;
      }
    },
    
    async loadChunkData(chunkIndex) {
      console.log(`Loading chunk ${chunkIndex}`);
      const chunkPath = buildDatasetPath(`genes/chunk_${chunkIndex}.bin`);
      console.log(`Genes: chunk: ${chunkPath}`);
      let response = await fetch(chunkPath);
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
      
      let chunkData = await response.arrayBuffer();
      console.log(chunkData,38666)
      let dataView = new DataView(chunkData);
      let allExps = [];
      const rowLength = this.geneData.total_cells; // 
      // 
      let rowCount = dataView.byteLength / (4 * rowLength); // 
      
      for(let i = 0; i < rowCount; i++) {
        let row = [];
        for(let j = 0; j < rowLength; j++) {
          let offset = (i * rowLength + j) * 4; // Float324
          row.push(dataView.getFloat32(offset, true));
        }
        allExps.push(row);
      }
      
      // 
      this.chunkCache[chunkIndex] = allExps;
      return allExps;
    },
    
    async getGeneData(geneName) {
      let index = this.geneList.findIndex(item=>item.value===geneName);
      // console.log(index, 180);
      let chunkIndex = Math.floor(index/this.geneData.genes_per_chunk);
      // console.log(chunkIndex, 182, this.geneData.genes_per_chunk);
      let geneIndex = index % this.geneData.genes_per_chunk;
      
      // 
      if(!this.chunkCache[chunkIndex]) {
        await this.loadChunkData(chunkIndex);
      }
      
      // console.log(Math.max(...this.chunkCache[chunkIndex][geneIndex]), 183);
      // 
      // console.log(`Getting expression data for gene ${geneName} at index ${geneIndex}`);
      return {exps: this.chunkCache[chunkIndex][geneIndex]};
    },
    
    // 
    generateHistogramData(geneName) {
      // 
      console.log(` ${geneName} `);
      
      if (!this.currentSelectedGeneInfo[geneName]) {
        console.warn(` ${geneName} `);
        return [];
      }
      
      const expressionData = this.currentSelectedGeneInfo[geneName];
      console.log(` ${geneName} : ${expressionData.length}`);
      console.log(` ${geneName} 10:`, expressionData.slice(0, 10));
      
      // 0
      const nonZeroValues = expressionData.filter(val => val > 0);
      console.log(` ${geneName} : ${nonZeroValues.length}`);
      
      if (nonZeroValues.length === 0) {
        console.warn(` ${geneName} `);
        return []; // 
      }
      
      // getExpressionRange
      const range = this.getExpressionRange(geneName);
      const min = range.min;
      const max = range.max;
      console.log(` ${geneName} : min=${min.toFixed(3)}, max=${max.toFixed(3)}`);
      
      // 
      const bins = 50; // 
      const binWidth = (max - min) / bins;
      
      // 
      const histogramData = Array(bins).fill(0).map((_, i) => {
        const binStart = min + i * binWidth;
        const binEnd = min + (i + 1) * binWidth;
        return {
          value: binStart, // 
          binStart: binStart,
          binEnd: binEnd,
          count: 0,
          height: 0
        };
      });
      
      // 
      nonZeroValues.forEach(val => {
        const binIndex = Math.min(Math.floor((val - min) / binWidth), bins - 1);
        if (binIndex >= 0) {
          histogramData[binIndex].count++;
        }
      });
      
      // 
      const maxCount = Math.max(...histogramData.map(d => d.count));
      histogramData.forEach(bin => {
        bin.height = bin.count > 0 ? (bin.count / maxCount) * 90 : 0; // 90px
      });
      
      // 
      const totalCounts = histogramData.reduce((sum, bin) => sum + bin.count, 0);
      const nonEmptyBins = histogramData.filter(bin => bin.count > 0).length;
      console.log(` ${geneName} : =${totalCounts}, =${nonEmptyBins}, =${maxCount}`);
      
      return histogramData;
    },
    
    // 
    getExpressionPercentage(geneName) {
      if (!this.currentSelectedGeneInfo[geneName]) return "0.0";
      
      const expressionData = this.currentSelectedGeneInfo[geneName];
      const nonZeroCount = expressionData.filter(val => val > 0).length;
      const percentage = (nonZeroCount / expressionData.length) * 100;
      return percentage.toFixed(1);
    },
    
    // 
    getAverageExpression(geneName) {
      if (!this.currentSelectedGeneInfo[geneName]) return "";
      
      const expressionData = this.currentSelectedGeneInfo[geneName];
      const sum = expressionData.reduce((acc, val) => acc + val, 0);
      const avg = sum / expressionData.length;
      
      return avg.toExponential(5);
    },
    
    // 
    getExpressionRange(geneName) {
      if (!this.currentSelectedGeneInfo[geneName]) {
        return { min: 0, max: 0 };
      }
      
      // 
      const expressionData = this.currentSelectedGeneInfo[geneName];
      // 0
      const nonZeroValues = expressionData.filter(val => val > 0);
      
      if (nonZeroValues.length === 0) {
        return { min: 0, max: 1 }; // 
      }
      
      // 
      const min = Math.min(...nonZeroValues);
      const max = Math.max(...nonZeroValues);
      
      return { min, max };
    },
    
    // 
    calculateExpressionValue(x, svgWidth, geneName) {
      const range = this.getExpressionRange(geneName);
      const min = range.min;
      const max = range.max;
      
      // 4%96%
      const leftMargin = svgWidth * 0.04; // 4%
      const rightMargin = svgWidth * 0.04; // 4%
      const effectiveWidth = svgWidth - leftMargin - rightMargin; // 92%
      
      // x
      const adjustedX = Math.max(0, Math.min(x - leftMargin, effectiveWidth));
      
      // [0,1]
      const normalizedPos = effectiveWidth > 0 ? adjustedX / effectiveWidth : 0;
      
      // 
      return min + normalizedPos * (max - min);
    },
    
    // 
    startSelection(event, geneName) {
      // 
      event.preventDefault();
      
      // 
      if (!this.currentSelectedGeneInfo[geneName]) return;
      
      this.selectionActive = true;
      this.currentSelectionGene = geneName;
      
      // SVG
      const svgElement = event.currentTarget;
      const rect = svgElement.getBoundingClientRect();
      
      // SVG
      const x = event.clientX - rect.left;
      
      // SVG
      this.selectionStart = { x: x };
      this.selectionEnd = { x: x };
      
      // 
      this.selectedRange[geneName] = {
        start: this.calculateExpressionValue(x, rect.width, geneName),
        end: this.calculateExpressionValue(x, rect.width, geneName)
      };
      
      // 
      this.saveSelectedRanges();
    },
    
    updateSelection(event, geneName) {
      // 
      event.preventDefault();
      
      if (!this.selectionActive || this.currentSelectionGene !== geneName) return;
      
      // SVG
      const svgElement = event.currentTarget;
      const rect = svgElement.getBoundingClientRect();
      
      // SVG
      const x = Math.max(0, Math.min(event.clientX - rect.left, rect.width));
      
      // 
      this.selectionEnd = { x: x };
      
      // 
      const startVal = this.calculateExpressionValue(this.selectionStart.x, rect.width, geneName);
      const endVal = this.calculateExpressionValue(x, rect.width, geneName);
      
      // 
      this.selectedRange[geneName] = {
        start: Math.min(startVal, endVal),
        end: Math.max(startVal, endVal)
      };
      
      // 
      // this.saveSelectedRanges();
    },
    
    endSelection(geneName) {
      if (!this.selectionActive || this.currentSelectionGene !== geneName) return;
      
      this.selectionActive = false;
      
      // 
      if (Math.abs(this.selectionEnd.x - this.selectionStart.x) < 5) {
        delete this.selectedRange[geneName];
        delete this.selectionStats[geneName];
        this.saveSelectedRanges();
        
        // 
        this.$emit("currentRangeSelected", {
          gene: geneName,
          range: null,
          stats: null,
          geneData: null
        });
        return;
      }
      
      // 
      this.calculateSelectionStats(geneName);
      
      // 
      this.improveSelectionAccuracy(geneName);
      
      // 
      const geneExpressionData = this.currentSelectedGeneInfo[geneName];
      
      // 
      this.$emit("currentRangeSelected", {
        gene: geneName,
        range: this.selectedRange[geneName],
        stats: this.selectionStats[geneName],
        geneData: geneExpressionData, // 
        color: this.geneColor
      });
      
      // 
      this.saveSelectedRanges();
      
      // 
      this.$forceUpdate();
    },
    
    cancelSelection(event) {
      if (event) {
        event.preventDefault();
      }
      this.selectionActive = false;
    },
    
    // 
    isInSelectionRange(bar, geneName) {
      const range = this.selectedRange[geneName];
      if (!range) return false;
      
      // 
      if (range.start === range.end) return false;
      
      // 
      const minVal = Math.min(range.start, range.end);
      const maxVal = Math.max(range.start, range.end);
      
      // 
      // 
      return (bar.binStart <= maxVal && bar.binEnd >= minVal);
    },
    
    //  - 
    getSelectionWidth(geneName) {
      if (!this.selectionActive || this.currentSelectionGene !== geneName) {
        return 0;
      }
      
      return Math.abs(this.selectionEnd.x - this.selectionStart.x);
    },
    
    scaledX(index) {
      return `${4 + (index * 92 / 50)}%`;
    },
    
    // 
    saveSelectedRanges() {
      try {
        localStorage.setItem('geneSelectionRanges', JSON.stringify(this.selectedRange));
        localStorage.setItem('geneSelectionStats', JSON.stringify(this.selectionStats));
      } catch (error) {
        console.error('Failed to save selection ranges:', error);
      }
    },
    
    // 
    loadSavedRanges() {
      try {
        const savedRanges = localStorage.getItem('geneSelectionRanges');
        const savedStats = localStorage.getItem('geneSelectionStats');
        
        if (savedRanges) {
          this.selectedRange = JSON.parse(savedRanges);
        }
        
        if (savedStats) {
          this.selectionStats = JSON.parse(savedStats);
        }
      } catch (error) {
        console.error('Failed to load selection ranges:', error);
      }
    },
    
    // 
    calculateSelectionStats(geneName) {
      if (!this.selectedRange[geneName] || !this.currentSelectedGeneInfo[geneName]) return;
      
      const range = this.selectedRange[geneName];
      const expressionData = this.currentSelectedGeneInfo[geneName];
      
      // 
      
      // 
      const minRange = Math.min(range.start, range.end);
      const maxRange = Math.max(range.start, range.end);
      
      // 
      const cellsInRange = expressionData.filter(val => val >= (minRange - 0.0001) && val <= (maxRange + 0.0001)).length;
      const totalCells = expressionData.length;
      const percentage = (cellsInRange / totalCells * 100).toFixed(1);
      
      // 
      this.selectionStats[geneName] = {
        count: cellsInRange,
        percentage: percentage
      };
    },
    
    // 
    getSelectionStats(geneName) {
      return this.selectionStats[geneName];
    },
    
    // 
    clearSelection(geneName) {
      delete this.selectedRange[geneName];
      delete this.selectionStats[geneName];
      this.saveSelectedRanges();
      
      // 
      this.$emit("currentRangeSelected", {
        gene: geneName,
        range: null,
        stats: null,
        geneData: null // 
      });
      
      // 
      this.$forceUpdate();
    },
    
    // 
    formatExpression(value) {
      return value.toFixed(2);
    },
    
    // SVG - 
    getSelectionRangePosition(geneName) {
      if (!this.selectedRange[geneName]) return { start: 0, width: 0 };
      
      const range = this.selectedRange[geneName];
      const svg = document.getElementById(`histogram-${geneName}`);
      if (!svg) return { start: 0, width: 0 };
      
      const svgWidth = svg.getBoundingClientRect().width;
      
      // 
      const exprRange = this.getExpressionRange(geneName);
      const min = exprRange.min;
      const max = exprRange.max;
      const totalRange = max - min;
      
      // 
      if (totalRange <= 0) return { start: 0, width: 0 };
      
      // 4%92%
      const leftMargin = svgWidth * 0.04; // 4%
      const effectiveWidth = svgWidth * 0.92; // 92%
      
      // 
      const startRatio = (range.start - min) / totalRange;
      const endRatio = (range.end - min) / totalRange;
      
      // SVG
      const startX = leftMargin + startRatio * effectiveWidth;
      const endX = leftMargin + endRatio * effectiveWidth;
      
      return {
        start: Math.min(startX, endX),
        width: Math.max(1, Math.abs(endX - startX))
      };
    },
    
    // X
    getAxisTickValue(geneName, position) {
      const range = this.getExpressionRange(geneName);
      const min = range.min;
      const max = range.max;
      
      // 
      return min + position * (max - min);
    },
    
    // 
    emitAllSelectionRanges() {
      // 
      const allRanges = {};
      
      for (const geneName in this.selectedRange) {
        if (this.selectedRange[geneName] && this.selectionStats[geneName]) {
          allRanges[geneName] = {
            gene: geneName,
            range: this.selectedRange[geneName],
            stats: this.selectionStats[geneName]
          };
        }
      }
      
      // 
      this.$emit("currentRangeSelected", allRanges[this.currentSelectionGene]||[]);
    },
    
    // 
    handlePanelClick(geneName,color=false) {
      this.currentOpenPanel = geneName;
      this.geneColor=color?color:this.geneColor
      // 
      if (this.selectedRange[geneName] && this.selectionStats[geneName]) {
        this.$emit("currentRangeSelected", {
          gene: geneName,
          range: this.selectedRange[geneName],
          stats: this.selectionStats[geneName],
          geneData: this.currentSelectedGeneInfo[geneName], // 
          color:this.geneColor
        });
      }
    },
    
    // 
    emitCurrentSelectionRange() {
      if (!this.currentOpenPanel) return;
      
      const geneName = this.currentOpenPanel;
      
      if (this.selectedRange[geneName] && this.selectionStats[geneName]) {
        this.$emit("currentRangeSelected", {
          gene: geneName,
          range: this.selectedRange[geneName],
          stats: this.selectionStats[geneName],
          geneData: this.currentSelectedGeneInfo[geneName] // 
        });
      }
    },
    handleColorClicked(item){
      this.geneColor=item
      this.$emit("getGeneColor",this.geneColor)
    },
    // 
    getBarColor(bar, geneName, index) {
      // 
      if (this.isInSelectionRange(bar, geneName)) {
        return '#1976D2'; // 
      }
      
      // geneColor
      if (this.geneColor) {
        // 
        const range = this.getExpressionRange(geneName);
        const { min, max } = range;
        const totalRange = max - min;
        
        // 0-1
        const position = (bar.value - min) / totalRange;
        
        // 
        //  - 
        const intensity = Math.round(160 + position * 90); // 160-250 ()
        return `rgb(25, ${intensity}, 210)`;
      }
      
      // 
      return '#E0E0E0';
    },
    handleColorIconClick(geneName) {
      console.log(`: ${geneName}`);
      
      // 
      this.activeGeneForRendering = geneName;
      
      // 
      if (this.currentSelectedGeneInfo[geneName]) {
        const singleGeneData = {
          [geneName]: this.currentSelectedGeneInfo[geneName]
        };
        
        console.log(` ${geneName} `);
        this.$emit("getGeneExp", singleGeneData);
        
        // 
        if (this.selectedRange[geneName] && this.selectionStats[geneName]) {
          this.$emit("currentRangeSelected", {
            gene: geneName,
            range: this.selectedRange[geneName],
            stats: this.selectionStats[geneName],
            geneData: this.currentSelectedGeneInfo[geneName], // 
            color: true // 
          });
        }
      }
    },
    /**
     * 
     * 
     */
    validateGeneDataDifferences() {
      console.log('===  ===');
      const geneNames = Object.keys(this.currentSelectedGeneInfo);
      
      if (geneNames.length < 2) {
        console.log('2');
        return;
      }
      
      for (let i = 0; i < geneNames.length; i++) {
        for (let j = i + 1; j < geneNames.length; j++) {
          const gene1 = geneNames[i];
          const gene2 = geneNames[j];
          const data1 = this.currentSelectedGeneInfo[gene1];
          const data2 = this.currentSelectedGeneInfo[gene2];
          
          // 10
          const sample1 = data1.slice(0, 10);
          const sample2 = data2.slice(0, 10);
          const areSame = JSON.stringify(sample1) === JSON.stringify(sample2);
          
          console.log(` ${gene1} vs ${gene2}:`);
          console.log(`  ${gene1} 10: [${sample1.join(', ')}]`);
          console.log(`  ${gene2} 10: [${sample2.join(', ')}]`);
          console.log(`  10: ${areSame}`);
          
          // 
          const stats1 = this.getGeneStats(gene1);
          const stats2 = this.getGeneStats(gene2);
          console.log(`  ${gene1} : ${JSON.stringify(stats1)}`);
          console.log(`  ${gene2} : ${JSON.stringify(stats2)}`);
        }
      }
      console.log('===  ===');
    },
    
    /**
     * 
     */
    getGeneStats(geneName) {
      const data = this.currentSelectedGeneInfo[geneName];
      if (!data) return null;
      
      const nonZeroValues = data.filter(val => val > 0);
      return {
        total: data.length,
        nonZero: nonZeroValues.length,
        min: nonZeroValues.length > 0 ? Math.min(...nonZeroValues).toFixed(3) : 0,
        max: nonZeroValues.length > 0 ? Math.max(...nonZeroValues).toFixed(3) : 0,
        avg: nonZeroValues.length > 0 ? (nonZeroValues.reduce((a, b) => a + b, 0) / nonZeroValues.length).toFixed(3) : 0
      };
    },
    
    /**
     * 
     * 
     */
    improveSelectionAccuracy(geneName) {
      if (!this.selectedRange[geneName]) return;
      
      const range = this.selectedRange[geneName];
      const histogramData = this.generateHistogramData(geneName);
      
      if (!histogramData || histogramData.length === 0) return;
      
      // 
      let adjustedStart = range.start;
      let adjustedEnd = range.end;
      
      // 
      let minStartDiff = Infinity;
      let minEndDiff = Infinity;
      
      histogramData.forEach(bar => {
        const startDiff = Math.abs(bar.binStart - range.start);
        const endDiff = Math.abs(bar.binEnd - range.end);
        
        if (startDiff < minStartDiff) {
          minStartDiff = startDiff;
          adjustedStart = bar.binStart;
        }
        
        if (endDiff < minEndDiff) {
          minEndDiff = endDiff;
          adjustedEnd = bar.binEnd;
        }
      });
      
      // 
      const startAdjustmentRatio = Math.abs(adjustedStart - range.start) / (range.end - range.start);
      const endAdjustmentRatio = Math.abs(adjustedEnd - range.end) / (range.end - range.start);
      
      console.log(` ${geneName} :`);
      console.log(`  : ${range.start.toFixed(3)} - ${range.end.toFixed(3)}`);
      console.log(`  : ${adjustedStart.toFixed(3)} - ${adjustedEnd.toFixed(3)}`);
      console.log(`  : =${(startAdjustmentRatio * 100).toFixed(1)}%, =${(endAdjustmentRatio * 100).toFixed(1)}%`);
      
      if (startAdjustmentRatio < 0.1 && endAdjustmentRatio < 0.1) {
        this.selectedRange[geneName] = {
          start: Math.min(adjustedStart, adjustedEnd),
          end: Math.max(adjustedStart, adjustedEnd)
        };
        
        console.log(`  `);
        
        // 
        this.calculateSelectionStats(geneName);
      } else {
        console.log(`  `);
      }
    },
  }
};
</script>

