<template>
  <div style="height: 100%">
    <div id="canvasContainer" style="height: 100%" ref="scatterBrain"></div>
    <!-- tooltipbody -->
  </div>
</template>

<script>
import ScatterRenderer from "@/render/ScatterRenderer";
import { perfTracker } from "@/render/performance";
import { mapState } from 'vuex';
import { buildDatasetPath, getDataset } from '@/api/config';

export default {
  props: {
    // 
    geneData: {
      type: Array,
      default: () => []
    },
    // 
    geneExpRange: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      renderer: null,
      selectedIndices: [],
      isInitialized: false,
      highlightListener: null,
      multipleHighlightListener: null,
      multiAttributeIntersectionListener: null,
      clearHighlightListener: null,
      hoverListener: null,
      clearHoverListener: null,
      mouseEnterListAreaListener: null,
      mouseLeaveListAreaListener: null,
      currentHighlight: null,
      currentCheckedHighlight: null
    };
  },

  computed: {
    ...mapState({
      operate: state => state.uiState.operateActive || 1,
      colorData: state => state.cellData.colorData || [],
      total: state => state.cellData.total || -1,
      colorBy: state => state.cellData.colorField || "",
      typeClass: state => state.uiState.typeClass || [],
      typeItem: state => state.uiState.typeItem || [],
      typeValue: state => state.uiState.typeValue || [],
      operatorType: state => state.uiState.operatorType || [],
      filterNum: state => state.uiState.filterNum || 0,
      field: state => state.cellData.field || [],
      fieldValue: state => state.cellData.fieldValue || {},
      hoverVal: state => state.cellData.hoverVal || -1
    })
  },

  watch: {
    operate: {
      handler(newMode) {
        this.switchMode(newMode);
      }
    },
    colorBy: {
      async handler(newColorBy) {
        if (!this.renderer || !this.isInitialized) return;
        
        // 
        const colorIndex = this.field.indexOf(newColorBy);
        if (colorIndex >= 0) {
          await this.renderer.setColorAttribute(colorIndex);
        }
      }
    },
    geneExpRange: {
      handler(newRange) {
        if (!this.renderer) return;
        
        console.log(':', newRange);
        
        // 
        if (this.geneData && this.geneData.length > 0 && newRange && newRange.range) {
          console.log('');
          this.renderer.updateGeneData(this.geneData, newRange);
        } else if (this.geneData && this.geneData.length > 0) {
          // 
          this.renderer.updateGeneData(this.geneData);
        }
        
        this.renderer.options.geneExpRange = newRange;
        this.renderer.state.isDirty = true; // 
      },
      deep: true
    },
    geneData: {
      handler(newGeneData) {
        if (!this.renderer || !this.isInitialized) return;
        
        console.log(':', newGeneData);
        
        // 
        if (this.renderer.updateGeneData) {
          const expressionRange = this.geneExpRange;
          if (expressionRange && expressionRange.range) {
            console.log(':', expressionRange);
            this.renderer.updateGeneData(newGeneData, expressionRange);
          } else {
          this.renderer.updateGeneData(newGeneData);
          }
        }
      },
      deep: true
    },
    // totalfield
    total: {
      handler(newTotal) {
        if (newTotal > 0 && !this.renderer && this.field && this.field.length > 0) {
          console.log(`total: ${newTotal}`);
          this.initRenderer();
        }
      },
      immediate: true
    },
    field: {
      handler(newField) {
        if (newField && newField.length > 0 && !this.renderer && this.total > 0) {
          console.log(`: ${newField.length}`);
          this.initRenderer();
        }
      },
      immediate: true
    },
    // 
    typeClass: {
      handler() {
        this.updateFilter();
      },
      deep: true
    },
    typeItem: {
      handler() {
        this.updateFilter();
      },
      deep: true
    },
    typeValue: {
      handler() {
        this.updateFilter();
      },
      deep: true
    },
    operatorType: {
      handler() {
        this.updateFilter();
      },
      deep: true
    },
    filterNum: {
      handler() {
        this.updateFilter();
      }
    }
  },

  mounted() {
    console.log('ScatterBrainSingle mounted - ');
    console.log(':', {
      total: this.total,
      fieldLength: this.field ? this.field.length : 0,
      colorBy: this.colorBy,
      geneDataLength: this.geneData ? this.geneData.length : 0
    });
    
    // 
    if (typeof window !== 'undefined') {
      window.addEventListener('datasetChanged', this.handleDatasetChange);
    }
    
    // 
    if (this.total > 0 && this.field && this.field.length > 0) {
      console.log(``);
      this.initRenderer();
    } else {
      console.log(`total: ${this.total}, : ${this.field ? this.field.length : 0}`);
      // watch
    }
    
    this.setupHighlightListeners();
  },

  beforeDestroy() {
    console.log('ScatterBrainSingle: ');
    
    // 
    if (typeof window !== 'undefined') {
      window.removeEventListener('datasetChanged', this.handleDatasetChange);
    }
    
    // 
    this.removeHighlightListeners();
    
    // 
    if (this.renderer) {
      try {
        // 
        if (typeof this.renderer.clearHoverCategory === 'function') {
          this.renderer.clearHoverCategory();
        }
        if (this.renderer.clearAttributeHighlight && typeof this.renderer.clearAttributeHighlight === 'function') {
          this.renderer.clearAttributeHighlight();
        }
        
        // ResizeObserver
        if (typeof this.renderer.dispose === 'function') {
          this.renderer.dispose();
        }
        
        console.log('ScatterBrainSingle: ');
      } catch (error) {
        console.error('ScatterBrainSingle: :', error);
      } finally {
        // 
        this.renderer = null;
      }
    }
    
    // 
    this.isInitialized = false;
    this.selectedIndices = [];
    this.currentHighlight = null;
    this.currentCheckedHighlight = null;
    
    console.log('ScatterBrainSingle: ');
  },

  methods: {
    /**
     * 
     */
    async handleDatasetChange(event) {
      console.log('ScatterBrainSingle: ', event.detail.dataset);
      
      //  buffer
      if (this.renderer) {
        try {
          // 
          const oldDataset = event.detail.oldDataset || this.datasetName;
          console.log(`: ${oldDataset} -> ${event.detail.dataset}`);
          
          await this.renderer.resetForNewDataset(oldDataset);
          
          // dispose
          if (this.renderer && typeof this.renderer.dispose === 'function') {
            this.renderer.dispose();
          }
        } catch (error) {
          console.error(':', error);
          // 
          if (this.renderer && typeof this.renderer.dispose === 'function') {
            try {
              this.renderer.dispose();
            } catch (disposeError) {
              console.error(':', disposeError);
            }
          }
        } finally {
          // 
          this.renderer = null;
        }
      }
      
      // 
      this.isInitialized = false;
      this.selectedIndices = [];
      this.currentHighlight = null;
      this.currentCheckedHighlight = null;
      
      // 
      setTimeout(() => {
        if (this.total > 0 && this.field && this.field.length > 0) {
          console.log('');
          this.initRenderer();
        }
      }, 500); // 
    },
    
    /**
     * 
     * 
     */
    updateFilter() {
      if (!this.renderer || !this.isInitialized) return;
      
      console.log(' ScatterBrainSingle.updateFilter ');
      console.log(':', {
        typeClass: this.typeClass,
        typeItem: this.typeItem,
        typeValue: this.typeValue,
        operatorType: this.operatorType,
        filterNum: this.filterNum
      });
      
      // updateFilter
      this.renderer.updateFilter({
        typeClass: this.typeClass,
        typeItem: this.typeItem,
        typeValue: this.typeValue,
        operatorType: this.operatorType,
        filterNum: this.filterNum
      });
    },
    
    /**
     * 
     */
    async initRenderer() {
      try {
        perfTracker.startMark('initializeRenderer');
        
        // totalfield
        if (this.total <= 0) {
          console.warn("total...");
          // 1
          setTimeout(() => {
            if (this.total > 0) {
              this.initRenderer();
            } else {
              console.warn("total");
            }
          }, 1000);
          return;
        }
        
        // 
        if (!this.field || !Array.isArray(this.field) || this.field.length === 0) {
          console.warn("...");
          // 1
          setTimeout(() => {
            if (this.field && this.field.length > 0) {
              this.initRenderer();
            } else {
              console.warn("");
            }
          }, 1000);
          return;
        }
        
        // 
        const container = this.$refs.scatterBrain;
        
        // 
        this.renderer = new ScatterRenderer(container, {
          pointSize: 3.0,
          defaultZoom: 1,
          defaultOffset: [0, 0],
          antialiasing: true,
          highlightSize: 1.5 // 
        });
        
        // 
        await this.renderer.initialize();
        
        // 
        this.renderer.callbacks.onPointSelected = this.handlePointSelected;
        
        // colorIndex
        const colorIndex = this.field.indexOf(this.colorBy);
        const validColorIndex = colorIndex >= 0 ? colorIndex : 0;
        
        console.log(`total: ${this.total}, : ${this.field.length}, colorBy: ${this.colorBy}`);
        
        // 
        await this.renderer.loadData({
          quadTreePath: buildDatasetPath('quad_tree.json'),
          total: this.total,
          field: this.field,
          colorBy: this.colorBy,
          colorIndex: validColorIndex
        });
        
        // 
        this.renderer.setMode(this.operate === 1 ? "pan" : "select");
        
        //  - Escape
        document.addEventListener('keydown', this.handleKeyDown);
        
        // 
        await this.renderer.setColorAttribute(validColorIndex);
        
        const duration = perfTracker.endMark('initializeRenderer');
        console.log(`: ${duration.toFixed(2)}ms`);
        
        this.isInitialized = true;
      } catch (error) {
        console.error("ScatterBrainSingle:", error);
      }
    },
    
    /**
     * 
     */
    setupHighlightListeners() {
      if (this.highlightListener) return;
      
      // 
      this.highlightListener = this.handleHighlightEvent.bind(this);
      this.multipleHighlightListener = this.handleMultipleHighlightEvent.bind(this);
      this.multiAttributeIntersectionListener = this.handleMultiAttributeIntersectionEvent.bind(this);
      this.clearHighlightListener = this.handleClearHighlightEvent.bind(this);
      this.hoverListener = this.handleHoverEvent.bind(this);
      this.clearHoverListener = this.handleClearHoverEvent.bind(this);
      this.mouseEnterListAreaListener = this.handleMouseEnterListArea.bind(this);
      this.mouseLeaveListAreaListener = this.handleMouseLeaveListArea.bind(this);
      
      // 
      document.addEventListener('highlight-attribute', this.highlightListener);
      document.addEventListener('highlight-multiple-attributes', this.multipleHighlightListener);
      document.addEventListener('highlight-multi-attribute-intersection', this.multiAttributeIntersectionListener);
      document.addEventListener('clear-highlight', this.clearHighlightListener);
      
      // 
      document.addEventListener('cell-property-item-mouseenter', this.hoverListener);
      document.addEventListener('cell-property-item-mouseleave', this.clearHoverListener);
      
      // 
      document.addEventListener('mouse-enter-list-area', this.mouseEnterListAreaListener);
      document.addEventListener('mouse-leave-list-area', this.mouseLeaveListAreaListener);
    },
    
    /**
     * 
     */
    removeHighlightListeners() {
      if (this.highlightListener) {
        document.removeEventListener('highlight-attribute', this.highlightListener);
        this.highlightListener = null;
      }
      
      if (this.multipleHighlightListener) {
        document.removeEventListener('highlight-multiple-attributes', this.multipleHighlightListener);
        this.multipleHighlightListener = null;
      }
      
      if (this.multiAttributeIntersectionListener) {
        document.removeEventListener('highlight-multi-attribute-intersection', this.multiAttributeIntersectionListener);
        this.multiAttributeIntersectionListener = null;
      }
      
      if (this.clearHighlightListener) {
        document.removeEventListener('clear-highlight', this.clearHighlightListener);
        this.clearHighlightListener = null;
      }
      
      if (this.hoverListener) {
        document.removeEventListener('cell-property-item-mouseenter', this.hoverListener);
        this.hoverListener = null;
      }
      
      if (this.clearHoverListener) {
        document.removeEventListener('cell-property-item-mouseleave', this.clearHoverListener);
        this.clearHoverListener = null;
      }
      
      if (this.mouseEnterListAreaListener) {
        document.removeEventListener('mouse-enter-list-area', this.mouseEnterListAreaListener);
        this.mouseEnterListAreaListener = null;
      }
      
      if (this.mouseLeaveListAreaListener) {
        document.removeEventListener('mouse-leave-list-area', this.mouseLeaveListAreaListener);
        this.mouseLeaveListAreaListener = null;
      }
    },
    
    /**
     * 
     * @param {CustomEvent} event - 
     */
    async handleHighlightEvent(event) {
      if (!this.renderer || !this.isInitialized) return;
      
      const { attributeName, attributeValue, isCheckHighlight } = event.detail;
      
      if (!attributeName || attributeValue === undefined) {
        console.warn('');
        return;
      }
      
      console.log(`: =${attributeName}, =${attributeValue}, =${isCheckHighlight}`);
      
      try {
        if (isCheckHighlight) {
          // 
          await this.renderer.loadAttributeAndHighlight(attributeName, attributeValue, true);
          
          // 
          this.currentCheckedHighlight = {
            attributeName,
            attributeValue, // attributeValue
            isCheckHighlight: true
          };
          
          console.log(`:`, this.currentCheckedHighlight);
        } else {
          // 
          await this.renderer.setHighlightAttribute(attributeName, attributeValue);
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     * @param {CustomEvent} event - 
     */
    async handleMultipleHighlightEvent(event) {
      if (!this.renderer || !this.isInitialized) return;
      
      const { attributeName, attributeValueIndices, isCheckHighlight } = event.detail;
      
      if (!attributeName || !Array.isArray(attributeValueIndices) || attributeValueIndices.length === 0) {
        console.warn('');
        return;
      }
      
      console.log(`: =${attributeName}, =[${attributeValueIndices.join(', ')}], =${isCheckHighlight}`);
      
      try {
        // 
        await this.renderer.setMultipleAttributeHighlight(attributeName, attributeValueIndices, isCheckHighlight);
        
        // 
        this.currentCheckedHighlight = {
          attributeName,
          attributeValueIndices: [...attributeValueIndices], // 
          isCheckHighlight: true
        };
        
        console.log(`:`, this.currentCheckedHighlight);
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     * @param {CustomEvent} event - 
     */
    async handleMultiAttributeIntersectionEvent(event) {
      if (!this.renderer || !this.isInitialized) return;
      
      const { attributeName, attributeValueIndices } = event.detail;
      
      if (!attributeName || !Array.isArray(attributeValueIndices)) {
        console.warn('');
        return;
      }
      
      console.log(`: =${attributeName}, =[${attributeValueIndices.join(', ')}]`);
      
      try {
        // 
        if (attributeValueIndices.length === 0) {
          console.log(` ${attributeName} `);
          
          // 
          await this.renderer.setMultiAttributeIntersectionHighlight(attributeName, [], true);
          
          // 
          if (this.renderer.state.multiAttributeHighlight.attributes.size === 0) {
            console.log('');
            this.currentCheckedHighlight = null;
          }
        } else {
          // 
          await this.renderer.setMultiAttributeIntersectionHighlight(attributeName, attributeValueIndices, true);
          
          // 
          this.currentCheckedHighlight = {
            mode: 'multi-attribute-intersection',
            isCheckHighlight: true
          };
          
          console.log(``);
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     */
    handleClearHighlightEvent(event) {
      if (!this.renderer || !this.isInitialized) {
        return;
      }
      
      console.log('', event.detail);
      
      try {
        // 
        const eventDetail = event.detail || {};
        const targetAttributeName = eventDetail.attributeName;
        const checkForCheckedItems = eventDetail.checkForCheckedItems;
        const force = eventDetail.force;
        const reason = eventDetail.reason;
        
        console.log(`: =${targetAttributeName}, =${checkForCheckedItems}, =${force}, =${reason}`);
        
        // 
        if (force) {
          console.log('');
          this.renderer.clearAttributeHighlight();
          this.currentHighlight = null;
          this.currentCheckedHighlight = null;
          return;
        }
        
        // 
        if (checkForCheckedItems && this.currentCheckedHighlight) {
          console.log('');
          return;
        }
        
        // 
        if (this.currentCheckedHighlight) {
          const checkedAttributeName = this.currentCheckedHighlight.attributeName;
          
          // 
          if (targetAttributeName && targetAttributeName !== checkedAttributeName) {
            console.log(` ${targetAttributeName}  ${checkedAttributeName}`);
            return;
          } else if (!targetAttributeName) {
            // 
            console.log('');
            return;
          }
        }
        
        // 
        console.log('');
        
        // 
        this.renderer.clearAttributeHighlight();
        
        // 
        this.currentHighlight = null;
        
        // 
        this.currentCheckedHighlight = null;
        console.log('');
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     * @param {Array} selectedPoints - 
     */
    handlePointSelected(selectedPoints) {
      console.log(`${selectedPoints.length}`);
      this.selectedIndices = selectedPoints;
      this.$emit('selectArea', selectedPoints);
    },
    
    /**
     * 
     * @param {KeyboardEvent} event - 
     */
    handleKeyDown(event) {
      // ESC
      if (event.key === 'Escape' && this.renderer) {
        this.renderer.setMode('pan');
      }
    },
    
    /**
     * 
     * @param {number} mode - 1: , 2: 
     */
    switchMode(mode) {
      if (!this.renderer) return;
      
      // 
      this.renderer.setMode(mode === 1 ? "pan" : "select");
    },
    
    /**
     * 
     */
    downloadImage() {
      if (this.renderer) {
        this.renderer.downloadCanvasAsPNG(`scatter_${this.colorBy}`);
      }
    },
    
    /**
     * 
     */
    backupDownload() {
      try {
        const container = document.querySelector('#canvasContainer');
        if (!container) return;
        
        const canvas = container.querySelector('canvas');
        if (!canvas) return;
        
        const link = document.createElement('a');
        link.download = `scatter_plot_${this.colorBy || 'default'}.png`;
        link.href = canvas.toDataURL('image/png');
        
        document.body.appendChild(link);
        link.click();
        
        setTimeout(() => {
          document.body.removeChild(link);
          URL.revokeObjectURL(link.href);
        }, 100);
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     *  - 
     * @param {CustomEvent} event - 
     */
    async handleHoverEvent(event) {
      if (!this.renderer || !this.isInitialized) return;
      
      try {
        // 
        if (!event.detail) {
          console.warn('');
          return;
        }
        
        const { valueIndex, attributeName, attributeValue } = event.detail;
        
        // 
        console.log(`: =${attributeName}, =${attributeValue}, =${valueIndex}`);
        
        // valueIndex
        if (valueIndex === undefined || valueIndex < 0) {
          console.warn(`: ${valueIndex}`);
          return;
        }
        
        // hoverCategory
        if (attributeName === this.colorBy) {
          console.log(`: ${attributeName}, : ${valueIndex}`);
          this.renderer.setHoverCategory(valueIndex);
        } else {
          // 
          console.log(`: ${attributeName}, : ${valueIndex}`);
          // isCheckHighlight=false
          await this.renderer.loadAttributeAndHighlight(attributeName, valueIndex, false);
        }
        
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     */
    async handleClearHoverEvent() {
      if (!this.renderer || !this.isInitialized) return;
      
      try {
        console.log('');
        
        // 
        if (this.currentCheckedHighlight) {
          console.log('...', this.currentCheckedHighlight);
          
          // 
          if (this.currentCheckedHighlight.mode === 'multi-attribute-intersection') {
            console.log('');
            // 
            await this.renderer.calculateIntersectionHighlight();
          } else {
            // 
            const { attributeName, attributeValueIndices, isCheckHighlight } = this.currentCheckedHighlight;
            
            // 
            if (Array.isArray(attributeValueIndices)) {
              // 
              await this.renderer.setMultipleAttributeHighlight(attributeName, attributeValueIndices, isCheckHighlight);
            } else {
              // attributeValue
              const attributeValue = this.currentCheckedHighlight.attributeValue;
              if (attributeValue !== undefined) {
                await this.renderer.loadAttributeAndHighlight(attributeName, attributeValue, isCheckHighlight);
              }
            }
          }
          
          // 
          this.renderer.clearHoverCategory();
        } else {
          // 
          this.renderer.clearHoverCategory();
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     */
    handleMouseEnterListArea() {
      if (!this.renderer || !this.isInitialized) return;
      
      console.log('ScatterBrainSingle: ');
      
      // 
      this.renderer.setMouseInListArea(true);
    },
    
    /**
     * 
     */
    handleMouseLeaveListArea() {
      if (!this.renderer || !this.isInitialized) return;
      
      console.log('ScatterBrainSingle: ');
      
      // 
      this.renderer.setMouseInListArea(false);
    },
    
    /**
     * 
     * Selected Cells
     */
    handleContainerResize() {
      if (!this.renderer || !this.isInitialized) {
        console.warn('ScatterBrainSingle: ');
        return;
      }
      
      console.log('ScatterBrainSingle: ');
      
      try {
        // 
        const container = this.$refs.scatterBrain;
        if (container) {
          const { offsetWidth, offsetHeight } = container;
          console.log(`ScatterBrainSingle:  - : ${offsetWidth}, : ${offsetHeight}`);
        }
        
        // nextTickDOM
        this.$nextTick(() => {
          // 
          setTimeout(() => {
            if (this.renderer && typeof this.renderer.handleResize === 'function') {
              console.log('ScatterBrainSingle: handleResize');
              this.renderer.handleResize();
              console.log('ScatterBrainSingle: ');
            } else {
              console.warn('ScatterBrainSingle: handleResize');
            }
          }, 100); // 
        });
      } catch (error) {
        console.error('ScatterBrainSingle: :', error);
      }
    },
    
    /**
     * 
     */
    clearSelection() {
      if (!this.renderer || !this.isInitialized) return;
      
      console.log('ScatterBrainSingle: ');
      
      try {
        if (typeof this.renderer.clearSelection === 'function') {
          this.renderer.clearSelection();
        }
      } catch (error) {
        console.error('ScatterBrainSingle: :', error);
      }
    },
    
    /**
     * 
     * @param {string} mode -  ('pan'  'select')
     */
    setMode(mode) {
      if (!this.renderer || !this.isInitialized) return;
      
      console.log(`ScatterBrainSingle:  ${mode}`);
      
      try {
        if (typeof this.renderer.setMode === 'function') {
          this.renderer.setMode(mode);
        }
      } catch (error) {
        console.error(`ScatterBrainSingle:  ${mode} :`, error);
      }
    }
  },
};
</script>

<style>
canvas {
  width: 100%;
  height: 100%;
  display: block; /* inline */
  user-select: none;  /*  */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  /* Canvas */
  box-sizing: border-box;
  /*  */
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  /* Canvas */
  position: relative;
  /* Canvas */
  object-fit: contain;
}

/*  */
#canvasContainer {
  position: relative;
  overflow: hidden; /*  */
  /*  */
  resize: both;
}

/* tooltip */
.tooltip-main {
  position: fixed !important; /* fixed */
  background-color: rgba(255, 255, 255, 0.98) !important;
  color: #333 !important;
  padding: 12px !important;
  text-align: left !important;
  border: 3px solid #ff0000 !important; /*  */
  border-radius: 6px !important;
