// 

// FPS
export class FpsCounter {
  constructor(updateInterval = 1000) {
    this.frames = 0;
    this.lastTime = performance.now();
    this.fps = 0;
    this.updateInterval = updateInterval;
  }

  update() {
    this.frames++;
    const now = performance.now();
    const elapsed = now - this.lastTime;

    if (elapsed >= this.updateInterval) {
      this.fps = (this.frames * 1000) / elapsed;
      this.lastTime = now;
      this.frames = 0;
    }
    
    return this.fps;
  }
}

// 
export class PerformanceTracker {
  constructor() {
    this.marks = new Map();
    this.timings = {};
    this.lastRenderTime = 0;
    this.averageRenderTime = 0;
    this.renderCount = 0;
  }

  startMark(name) {
    this.marks.set(name, performance.now());
  }

  endMark(name) {
    if (!this.marks.has(name)) return 0;
    
    const startTime = this.marks.get(name);
    const duration = performance.now() - startTime;
    
    if (!this.timings[name]) {
      this.timings[name] = {
        total: 0,
        count: 0,
        average: 0,
        min: duration,
        max: duration
      };
    }
    
    const timing = this.timings[name];
    timing.total += duration;
    timing.count++;
    timing.average = timing.total / timing.count;
    timing.min = Math.min(timing.min, duration);
    timing.max = Math.max(timing.max, duration);
    
    this.marks.delete(name);
    return duration;
  }

  trackRenderTime(duration) {
    this.lastRenderTime = duration;
    this.renderCount++;
    this.averageRenderTime = ((this.averageRenderTime * (this.renderCount - 1)) + duration) / this.renderCount;
  }

  getTimings() {
    return this.timings;
  }

  getAverageRenderTime() {
    return this.averageRenderTime;
  }

  clear() {
    this.marks.clear();
    this.timings = {};
  }
}

// 
export class PerformanceWatcher {
  constructor(thresholds = {}) {
    this.thresholds = {
      renderTime: 16, // 16ms (60fps)
      loadTime: 500,  // 500ms
      memoryLimit: 200 * 1024 * 1024, // 200MB
      ...thresholds
    };
    this.warnings = [];
  }

  checkRenderTime(duration) {
    if (duration > this.thresholds.renderTime) {
      const warning = `(${duration.toFixed(2)}ms)(${this.thresholds.renderTime}ms)`;
      this.warnings.push({type: 'render', message: warning, time: performance.now()});
      return warning;
    }
    return null;
  }

  checkLoadTime(duration) {
    if (duration > this.thresholds.loadTime) {
      const warning = `(${duration.toFixed(2)}ms)(${this.thresholds.loadTime}ms)`;
      this.warnings.push({type: 'load', message: warning, time: performance.now()});
      return warning;
    }
    return null;
  }

  checkMemoryUsage() {
    if (window.performance && window.performance.memory) {
      const memory = window.performance.memory.usedJSHeapSize;
      if (memory > this.thresholds.memoryLimit) {
        const warning = `(${(memory/1024/1024).toFixed(2)}MB)(${(this.thresholds.memoryLimit/1024/1024).toFixed(2)}MB)`;
        this.warnings.push({type: 'memory', message: warning, time: performance.now()});
        return warning;
      }
    }
    return null;
  }

  getWarnings() {
    return this.warnings;
  }

  clearWarnings() {
    this.warnings = [];
  }
}

// 
export const perfTracker = new PerformanceTracker();
export const perfWatcher = new PerformanceWatcher();
export const fpsCounter = new FpsCounter(); 