class QuadNode {
    constructor(data) {
      this.id = data.id;
      this.boundary = data.boundary; 
      this.pointsCount = data.points_count || 0;
      this.hasFile = data.has_file || false;
      this.children = null;
      this.points = null;
      
      // 
      if (data.children) {
        this.children = {
          nw: data.children.nw ? new QuadNode(data.children.nw) : null,
          ne: data.children.ne ? new QuadNode(data.children.ne) : null,
          sw: data.children.sw ? new QuadNode(data.children.sw) : null,
          se: data.children.se ? new QuadNode(data.children.se) : null
        };
      }
    }

    // 
    containsPoint(x, y) {
      if (!this.boundary) return false;
      const [minX, minY, maxX, maxY] = this.boundary;
      return x >= minX && x <= maxX && y >= minY && y <= maxY;
    }

    // 
    getArea() {
      if (!this.boundary) return 0;
      const [minX, minY, maxX, maxY] = this.boundary;
      return (maxX - minX) * (maxY - minY);
    }

    // 
    intersectsBounds(searchBounds) {
      if (!this.boundary) return false;
      const [nodeMinX, nodeMinY, nodeMaxX, nodeMaxY] = this.boundary;
      return !(
        nodeMaxX < searchBounds.minX || 
        nodeMinX > searchBounds.maxX ||
        nodeMaxY < searchBounds.minY || 
        nodeMinY > searchBounds.maxY
      );
    }
  }
  
  class QuadTree {
    constructor(jsonData) {
      if (!jsonData || !jsonData.root) {
        throw new Error('');
      }
      this.blocksCount = jsonData.total_blocks || 0;
      this.root = new QuadNode(jsonData.root);
      this.nodeCache = new Map(); // 
      this.cacheLimit = 1000; // 
      
      // 
      this.stats = {
        cacheHits: 0,
        cacheMisses: 0,
        totalSearches: 0
      };
    }
  
    // 
    getNodeArea(node) {
      return node ? node.getArea() : 0;
    }

    // 
    findNodeByPoint(x, y) {
      this.stats.totalSearches++;
      
      // 
      const cacheKey = `point_${x.toFixed(3)}_${y.toFixed(3)}`;
      if (this.nodeCache.has(cacheKey)) {
        this.stats.cacheHits++;
        return this.nodeCache.get(cacheKey);
      }
      
      this.stats.cacheMisses++;
      const result = this._findNodeByPointInternal(this.root, x, y);
      
      // 
      if (this.nodeCache.size >= this.cacheLimit) {
        // LRU
        const firstKey = this.nodeCache.keys().next().value;
        this.nodeCache.delete(firstKey);
      }
      this.nodeCache.set(cacheKey, result);
      return result;
    }
    
    // 
    _findNodeByPointInternal(node, x, y, depth = 0) {
      if (!node || !node.containsPoint(x, y)) return null;

      // 
      if (!node.children) {
        return node;
      }
      
      // 
      let smallestNode = null;
      let smallestArea = Infinity;
      
      for (const child of Object.values(node.children)) {
        if (!child || !child.containsPoint(x, y)) continue;
        
        const result = this._findNodeByPointInternal(child, x, y, depth + 1);
        if (result) {
          const area = this.getNodeArea(result);
          if (!smallestNode || area < smallestArea) {
            smallestNode = result;
            smallestArea = area;
          }
        }
      }
      
      // 
      return (smallestNode && smallestNode.pointsCount > 0) ? smallestNode : node;
    }

    // 
    findMultiplePoints(points) {
      const results = new Map();
      for (const point of points) {
        results.set(point, this.findNodeByPoint(point[0], point[1]));
      }
      return results;
    }

    // 
    findNodesInRange(node, searchBounds) {
      if (!node) return [];
      
      // 
      if (!node.intersectsBounds(searchBounds)) {
        return [];
      }
      
      let nodes = [];
      
      // 
      if (node.hasFile) {
        nodes.push(node);
      }

      // 
      if (node.children) {
        for (const child of Object.values(node.children)) {
          if (child) {
            //  push(...)  concat 
            nodes.push(...this.findNodesInRange(child, searchBounds));
          }
        }
      }
      
      return nodes;
    }

    // 
    getPerformanceStats() {
      const cacheHitRate = this.stats.totalSearches > 0 
        ? (this.stats.cacheHits / this.stats.totalSearches * 100).toFixed(2) 
        : 0;
      
      return {
        ...this.stats,
        cacheHitRate: `${cacheHitRate}%`,
        cacheSize: this.nodeCache.size
      };
    }

    // 
    clearCache() {
      this.nodeCache.clear();
      // 
      this.stats = {
        cacheHits: 0,
        cacheMisses: 0,
        totalSearches: 0
      };
    }
  }
  export {QuadNode,QuadTree}