<!--
 * 
 * SingleCellGraph.vue
 -->
<template>
  <div class="graph-controls">
    <v-row class="graph-row" :class="{'flex-column': $vuetify.breakpoint.xs}">
      <v-col
        :cols="$vuetify.breakpoint.xs ? 12 : (5 / graphNum)"
        class="dataset-info"
      >
        <p class="mb-2 dataset-name text-truncate">
          {{ datasetName }}
        </p>
        <p class="status-line">
          <svg
            viewBox="0 0 24 24"
            role="img"
            aria-hidden="true"
            class="v-icon__svg mr-1"
          >
            <path
              d="M12,19.58V19.58C10.4,19.58 8.89,18.96 7.76,17.83C6.62,16.69 6,15.19 6,13.58C6,12 6.62,10.47 7.76,9.34L12,5.1M17.66,7.93L12,2.27V2.27L6.34,7.93C3.22,11.05 3.22,16.12 6.34,19.24C7.9,20.8 9.95,21.58 12,21.58C14.05,21.58 16.1,20.8 17.66,19.24C20.78,16.12 20.78,11.05 17.66,7.93Z"
            ></path>
          </svg>
          <span class="mr-2 text-truncate">{{ colorBy }}</span>
          <v-icon class="mr-1" small>mdi-link</v-icon>
          <span class="mr-2" v-if="!filterNum">None</span>
          <span class="mr-2" v-else>{{ filterNum }} filters</span>
          <v-icon class="mr-1" small>mdi-camera-outline</v-icon>
          <span>Sync Camera</span>
        </p>
      </v-col>
      <v-col
        :cols="$vuetify.breakpoint.xs ? 12 : (7 / graphNum)"
        class="control-buttons"
      >
        <div class="icons-container">
          <v-icon
            class="graph-icon"
            :class="{ opearate_active: operateActive === 1 }"
            @click="changeOperator(1)"
            >mdi-arrow-expand-all</v-icon
          >
          <v-icon
            class="graph-icon"
            :class="{ opearate_active: operateActive === 2 }"
            @click="openSelectDrawer"
            >mdi-select-arrow-up</v-icon
          >
          <v-divider
            class="mx-2"
            inset
            vertical
            style="height: 20px"
          ></v-divider>
          <v-icon
            class="graph-icon"
            :class="{ opearate_active: operateActive === 3 }"
            @click="separateGraph"
            >mdi-plus-circle-multiple-outline</v-icon
          >
          <v-icon
            class="graph-icon"
            :class="{ opearate_active: operateActive === 4 }"
            @click="downloadImage"
            >mdi-arrow-down</v-icon
          >
          <v-icon
            class="graph-icon"
            :class="{ opearate_active: operateActive === 5 }"
            @click="deleteSeparateGraph"
            >mdi-delete</v-icon
          >
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'GraphControls',
  props: {
    // 
    datasetName: {
      type: String,
      default: ''
    },
    // 
    colorBy: {
      type: String,
      default: ''
    },
    // 
    filterNum: {
      type: Number,
      default: 0
    },
    // 
    operateActive: {
      type: Number,
      default: 1
    },
    // 
    graphNum: {
      type: Number,
      default: 1
    }
  },
  methods: {
    ...mapActions('uiState', [
      'setOperateActive',
      'openDrawer'
    ]),
    
    /**
     * 
     * @param {number} type - 
     */
    changeOperator(type) {
      this.setOperateActive(type);
      this.$emit('changeOperator', type);
    },
    
    /**
     * 
     */
    openSelectDrawer() {
      this.setOperateActive(2);
      this.openDrawer();
      this.$emit('openSelectDrawer');
    },
    
    /**
     * 
     */
    separateGraph() {
      this.setOperateActive(3);
      this.$emit('separateGraph');
    },
    
    /**
     * 
     */
    deleteSeparateGraph() {
      this.setOperateActive(5);
      this.$emit('deleteSeparateGraph');
    },
    
    /**
     * PNG
     */
    downloadImage() {
      this.$emit('downloadImage');
    }
  }
};
</script>

