export function stringToNumberSum(str) {
    let sum = 0;
    for (let i = 0; i < str.length; i++) {
      sum += str.charCodeAt(i);
    }
    return sum;
  }
  export function createMultiColorInterpolator(colors) {
    // 
    if (!Array.isArray(colors) || colors.length < 2) {
        throw new Error('At least two colors are required.');
    }

    //  [r, g, b, a]
    const colorStops = colors.map(color => {
        if (color.length === 3) { //  RGB  alpha  255
            return [...color, 255];
        }
        return color;
    });

    // 
    return function interpolateColor(value, min, max) {
        //  value  min  max 
        value = Math.max(min, Math.min(max, value));

        // 
        const totalStops = colorStops.length - 1;

        // 
        const ratio = (value - min) / (max - min);
        const index = Math.floor(ratio * totalStops);

        // 
        const subRatio = (ratio * totalStops - index) * (colorStops[index + 1].length - 1);

        // 
        const interpolatedColor = colorStops[index].map((channel, i) => {
            return Math.round(channel * (1 - subRatio) + colorStops[index + 1][i] * subRatio);
        });

        //  WebGL  0-1  RGBA
        return interpolatedColor.map(c => c / 255);
    };
}

export function  hsv2rgb(h, s, v) {
    let rgb = [0, 0, 0];
    let i = Math.floor(h * 6);
    let f = h * 6 - i;
    let p = v * (1 - s);
    let q = v * (1 - f * s);
    let t = v * (1 - (1 - f) * s);

    switch (i % 6) {
      case 0:
        rgb = [v, t, p];
        break;
      case 1:
        rgb = [q, v, p];
        break;
      case 2:
        rgb = [p, v, t];
        break;
      case 3:
        rgb = [p, q, v];
        break;
      case 4:
        rgb = [t, p, v];
        break;
      case 5:
        rgb = [v, p, q];
        break;
    }

    return rgb;
  }

export function getColorFromValue(value, colorIndex) {
    if (value < 0) {
      return [0.4, 0.4, 0.4, 1.0]; // 
    }

    // 1
    
    // 1.  - 
    const baseHue = (value * 0.618033988749895) % 1.0;
    
    // 2.  - 
    const colorGroup = Math.floor(value % 5);
    const secondaryShift = colorGroup * 0.2 + ((colorIndex * 0.1) % 0.2);
    
    // 3. 
    const hue = (baseHue + secondaryShift) % 1.0;
    
    // 4.  - 
    const saturation = 0.6 + ((value % 1) * 0.3);
    
    // 5.  - 
    const brightness = 0.7 + ((value * 7) % 1) * 0.2;
    
    // RGB
    const rgb = hsv2rgb(hue, saturation, brightness);

    // RGBA
    return [...rgb, 0.8];
}