<template>
  <div class="tab-panel" style="position: relative;overflow:visible;">
    <div class="vm-header br">
      <p style="font-size: 14px; margin: 10px 0">MANAGE LAYOUT</p>
      <p style="font-weight: 700; font-size: 14px; color: #646465">
        Current Views
      </p>
      <p style="font-size: 14px; margin: 5px 0">1 out of 4</p>
      <div v-if="isConfigLoading" style="font-size: 12px; color: #999; margin: 5px 0">
        Loading datasets configuration...
      </div>
    </div>
    
    <div class="br row2">
      <div class="color-main">
        <span class="color-block"></span>
      </div>
    </div>
    
    <div class="br row4 hover-active" @click="openEdit" v-for="(item,index) in allVisualization" :key="'e'+index">
      <div class="color-info">
        <span class="color-block"></span>
        <div style="text-align: left; margin-left: 20px" class="vm-color-text">
          <p>{{ currentDatasetInfo.title }}</p>
          <p style="color: #898989">{{ currentDatasetInfo.description }}</p>
          <p style="color: #898989; font-size: 12px; letter-spacing: 1px">
            {{ currentDatasetInfo.cellsDisplay }} <span style="font-weight: 700"></span> {{ currentDatasetInfo.colorField }}
          </p>
        </div>
      </div>
      <span>
        <v-icon class="arrow-right">mdi-chevron-right</v-icon>
      </span>
    </div>
    
    <div class="br row4 hover-active" v-if="allVisualization.length<4" @click="openAdd">
      <p style="width: 80%; margin: 0 auto; text-align: left">
        <v-icon style="margin-right: 15px; color: #141414">mdi-plus</v-icon>
        <span>Add Visualization</span>
      </p>
      <span>
        <v-icon class="arrow-right">mdi-chevron-right</v-icon>
      </span>
    </div>
    <div class="br row4 row5" v-for="i in (4-allVisualization.length-1)>0?4-allVisualization.length-1:0" :key="'r'+i">
      <p>None</p>
    </div>
    <div class="expand-panel"></div>
    
    <!--  -->
    <div class="vm-row2-click-panel" v-if="showEditClass">
      <v-row justify="space-between">
        <v-col cols="3">
          <h3 style="font-weight: 400; text-align: left">Edit</h3>
        </v-col>
        <v-col cols="3">
          <v-icon class="icon-set icon10" @click="showEditClass=!showEditClass">mdi-close</v-icon>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <p class="edit-title">Dataset</p>
          <v-autocomplete
            v-model="selectedDataset"
            :items="datasetOptions"
            dense
            clearable
            item-text="text"
            item-value="value"
            @change="handleDatasetChange"
          ></v-autocomplete>
          <p class="edit-title">Visualization</p>
          <v-autocomplete
            v-model="selectVisual"
            :items="visualList"
            dense
            clearable
             item-text="label"
            item-value="value"
          ></v-autocomplete>
          <div style="text-align: right">
            <v-btn text style="margin-right: 10px" color="#334c67">
              duplicate
            </v-btn>
            <v-btn depressed disabled> remove </v-btn>
          </div>
        </v-col>
      </v-row>
      <v-row style="padding: 0 0 0 20px">
        <v-col cols="12">
          <v-subheader style="color: #000">RESOLUTION / PERFORMANCE</v-subheader>
          <v-slider
            v-model="resulution"
            :tick-labels="ticksLabels"
            thumb-color="#334c67"
            track-color="#b1bbc5"
            track-fill-color="#334c67"
            :max="3"
            step="1"
            ticks="always"
            tick-size="2"
          ></v-slider>
        </v-col>
        <v-col cols="12">
          <v-subheader style="color: #000">POINT SIZE</v-subheader>
          <v-slider
            v-model="pointSize"
            :tick-labels="['Small', '', 'Medium', '', 'Large']"
            thumb-color="#334c67"
            track-color="#b1bbc5"
            track-fill-color="#334c67"
            :max="4"
            step="1"
            ticks="always"
            tick-size="2"
          ></v-slider>
        </v-col>
        <v-col cols="12">
          <v-subheader style="color: #000">FILTER</v-subheader>
          <v-checkbox
            v-model="checkbox"
            label="Transparent filtered cells"
          ></v-checkbox>
           <v-slider
           v-if="checkbox"
            v-model="filteredCell"
            :tick-labels="['0','','1']"
            thumb-color="#334c67"
            track-color="#b1bbc5"
            track-fill-color="#334c67"
            :max="2"
            step="1"
            ticks="always"
            tick-size="2"
          ></v-slider>
        </v-col>
      </v-row>
    </div>
    
    <!--  -->
 <div class="vm-row2-click-panel" v-if="showAddVis">
      <v-row justify="space-between">
        <v-col cols="3">
          <h3 style="font-weight: 400; text-align: left">Add</h3>
        </v-col>
        <v-col cols="3">
          <v-icon class="icon-set icon10" @click="showAddVis=!showAddVis">mdi-close</v-icon>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-autocomplete
            v-model="newSelectDataset"
            :items="datasetOptions"
            dense
            placeholder="Search"
            label="Dataset"
            clearable
            item-text="text"
            item-value="value"
             @change="changeAddDataset"
          ></v-autocomplete>
        </v-col>
        <v-col cols="12">
          <v-autocomplete
            v-model="newSelectVisual"
            :items="newVisualList"
            dense
            placeholder="Search"
            label="Visualization"
            clearable
               item-text="label"
            item-value="value"
          ></v-autocomplete>
        </v-col>
         <v-col cols="12">
           <div style="text-align: right">
            <v-btn text style="margin-right: 10px" color="#334c67">
              duplicate
            </v-btn>
            <v-btn depressed disabled> remove </v-btn>
          </div>
         </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import { 
  setDataset, 
  getDataset, 
  buildDatasetPath, 
  loadDatasetsConfig,
  getDatasetsConfig,
  getDatasetInfo,
  initializeDatasetConfig
} from '@/api/config';

export default {
  name: "ViewManager",
  data: () => ({
    seasons: ["Winter", "Spring", "Summer", "Fall"],
    icons: ["mdi-snowflake", "mdi-leaf", "mdi-fire", "mdi-water"],
    ticksLabels: ["Low / Fast", "", "", "High / Show"],
    pointSize: 1,
    resulution: 0,
    filteredCell: 1,
    checkbox: false,
    showEditClass: false,
    showAddVis: false,
    selectDataset: "single",
    
    // 
    selectedDataset: null, // mounted
    isDatasetLoading: false,
    datasetInfoCache: new Map(),
    
    //  - 
    datasetOptions: [],
    isConfigLoading: true,
    
    //  - 
    visualList: [],
    selectVisual: 'umap',
    newSelectDataset: '', 
    newSelectVisual: '', 
    newVisualList: [],
    allVisualization: [{
      visual: 'umap'
    }]
  }),
  
  computed: {
    ...mapState('cellData', [
      'datasetList'
    ]),
    
    /**
     *  - 
     */
    currentDatasetInfo() {
      const currentOption = this.datasetOptions.find(opt => opt.value === this.selectedDataset);
      if (currentOption && currentOption.info) {
        const config = currentOption.config;
        const info = currentOption.info;
        
        return {
          title: `${config.displayName || config.name}`,
          description: config.description || `${config.name}`,
          cellsDisplay: info ? this.formatNumber(info.total_cells) + ' cells' : 
                       config.metadata?.estimatedCells ? this.formatNumber(config.metadata.estimatedCells) + ' cells' : 
                       'Unknown cells',
          colorField: 'Class'
        };
      }
      
      // 
      return {
      };
    }
  },
  
  async mounted() {
    // 
    if (typeof window !== 'undefined') {
      window.addEventListener('datasetChanged', this.handleDatasetChangedEvent);
    }
    
    // 
    await this.initializeDynamicConfig();
    
    // 
    await this.loadAllDatasetInfo();
  },
  
  beforeDestroy() {
    if (typeof window !== 'undefined') {
      window.removeEventListener('datasetChanged', this.handleDatasetChangedEvent);
    }
  },
  
  methods: {
    ...mapActions('cellData', [
      'loadFieldsInfo',
      'switchDataSource'
    ]),
    
    /**
     * 
     */
    async initializeDynamicConfig() {
      try {
        this.isConfigLoading = true;
        
        // 
        await initializeDatasetConfig();
        
        // 
        const config = getDatasetsConfig();
        if (!config) {
          throw new Error('');
        }
        
        // 
        this.datasetOptions = config.datasets.map(dataset => ({
          text: dataset.displayName || dataset.name,
          value: dataset.id,
          config: dataset,
          info: null // loadAllDatasetInfo
        }));
        
        // 
        this.buildVisualizationOptions(config);
        
        // 
        const currentDataset = getDataset();
        // 
        if (currentDataset && this.datasetOptions.find(opt => opt.value === currentDataset)) {
          this.selectedDataset = currentDataset;
        } else {
          // 
          this.selectedDataset = config.defaultDataset || (this.datasetOptions.length > 0 ? this.datasetOptions[0].value : null);
        }
        
        console.log(':', {
          datasets: this.datasetOptions.length,
          currentDataset: this.selectedDataset,
          configDefault: config.defaultDataset,
          availableDatasets: this.datasetOptions.map(opt => opt.value)
        });
        
      } catch (error) {
        console.error(':', error);
        
        // 
        try {
          console.warn('datasets_config.json...');
          const config = await loadDatasetsConfig();
          
          if (config && config.datasets) {
            // 
            this.datasetOptions = config.datasets.map(dataset => ({
              text: dataset.displayName || dataset.name,
              value: dataset.id,
              config: dataset,
              info: null
            }));
            
            this.buildVisualizationOptions(config);
            
            // 
            this.selectedDataset = config.defaultDataset || (config.datasets.length > 0 ? config.datasets[0].id : null);
            
            console.log(':', {
              datasets: this.datasetOptions.length,
              defaultDataset: this.selectedDataset
            });
          } else {
            throw new Error('');
          }
        } catch (fallbackError) {
          console.error(':', fallbackError);
          
          // 
          this.datasetOptions = [
            {
              text: 'Single Outputs scRNAseq',
              value: 'Tumour',
              config: {
                id: 'Tumour',
                name: 'Tumour',
                displayName: 'Single Outputs scRNAseq',
                description: 'Main single cell RNA-seq dataset',
                supportedVisualizations: ['tsne', 'umap', 'pca']
              },
              info: null
            },
            {
              text: 'Single Outputs1 scRNAseq',
              value: 'mouse',
              config: {
                id: 'mouse',
                name: 'mouse',
                displayName: 'Single Outputs1 scRNAseq',
                description: 'Alternative single cell RNA-seq dataset',
                supportedVisualizations: ['tsne', 'umap', 'pca']
              },
              info: null
            }
          ];
          
          this.selectedDataset = 'Tumour'; // 
          
          this.buildVisualizationOptions({
            visualizations: {
              tsne: { label: 't-SNE' },
              umap: { label: 'UMAP' },
              pca: { label: 'PCA' }
            }
          });
        }
        
      } finally {
        this.isConfigLoading = false;
      }
    },
    
    /**
     * 
     */
    buildVisualizationOptions(config) {
      const visualizations = config.visualizations || {
        tsne: { label: 't-SNE', description: 't-Distributed Stochastic Neighbor Embedding' },
        umap: { label: 'UMAP', description: 'Uniform Manifold Approximation and Projection' },
        pca: { label: 'PCA', description: 'Principal Component Analysis' }
      };
      
      // 
      this.visualList = Object.entries(visualizations).map(([key, value]) => ({
        label: value.label || key.toUpperCase(),
        value: key
      }));
      
      this.newVisualList = [...this.visualList];
      
      // 
      if (!this.selectVisual && this.visualList.length > 0) {
        this.selectVisual = this.visualList.find(v => v.value === 'umap')?.value || this.visualList[0].value;
      }
    },
    
         /**
      * 
      */
     async handleDatasetChange(newDataset) {
       if (this.isDatasetLoading) return;
       this.showEditClass = false;
       this.isDatasetLoading = true;
       
       try {
         console.log(`ViewManager:  ${newDataset}`);
         
         // 
         setDataset(newDataset);
         
         // 
         if (typeof window !== 'undefined') {
           window.dispatchEvent(new CustomEvent('datasetChanged', {
             detail: { dataset: newDataset }
           }));
         }
         
         // 
         this.$emit('dataset-changed', {
           dataset: newDataset,
           info: this.getDatasetInfo(newDataset)
         });
         
         console.log(`: ${this.getDatasetDisplayName(newDataset)}`);
         
       } catch (error) {
         console.error('ViewManager: :', error);
         // 
         this.selectedDataset = getDataset();
       } finally {
         this.isDatasetLoading = false;
       }
     },
    
    /**
     * 
     */
    handleDatasetChangedEvent(event) {
      this.selectedDataset = event.detail.dataset;
    },
    
    /**
     * 
     */
    async loadAllDatasetInfo() {
      if (!this.datasetOptions || this.datasetOptions.length === 0) {
        console.warn('');
        return;
      }
      
      console.log('...');
      
      const loadPromises = this.datasetOptions.map(async (option) => {
        try {
          const info = await this.loadDatasetInfo(option.value);
          option.info = info;
          console.log(` ${option.value} :`, info);
        } catch (error) {
          console.warn(`: ${option.value}`, error);
          // 
          if (option.config && option.config.metadata && option.config.metadata.estimatedCells) {
            option.info = {
              total_cells: option.config.metadata.estimatedCells,
              estimated: true
            };
          }
        }
      });
      
      await Promise.all(loadPromises);
      console.log('');
    },
    
    /**
     * 
     */
    async loadDatasetInfo(datasetType) {
      // 
      if (this.datasetInfoCache.has(datasetType)) {
        return this.datasetInfoCache.get(datasetType);
      }
      
      try {
        const matrixInfoPath = buildDatasetPath('matrix_info.json', datasetType);
        const response = await fetch(matrixInfoPath);
        
        if (response.ok) {
          const info = await response.json();
          this.datasetInfoCache.set(datasetType, info);
          return info;
        } else {
          throw new Error(`HTTP ${response.status}`);
        }
      } catch (error) {
        console.error(`: ${datasetType}`, error);
        return null;
      }
    },
    
    /**
     * 
     */
    getDatasetInfo(datasetType) {
      const option = this.datasetOptions.find(opt => opt.value === datasetType);
      return option ? option.info : null;
    },
    
    /**
     * 
     */
    getDatasetDisplayName(datasetType) {
      const option = this.datasetOptions.find(opt => opt.value === datasetType);
      return option ? option.text : datasetType;
    },
    
    /**
     * 
     */
    getDatasetVisualizationOptions(datasetType) {
      const option = this.datasetOptions.find(opt => opt.value === datasetType);
      if (option && option.config && option.config.supportedVisualizations) {
        return this.visualList.filter(vis => 
          option.config.supportedVisualizations.includes(vis.value)
        );
      }
      return this.visualList; // 
    },
    
    /**
     * 
     */
    formatNumber(num) {
      if (!num) return '0';
      if (num >= 1000000) {
        return (num / 1000000).toFixed(1) + 'M';
      } else if (num >= 1000) {
        return (num / 1000).toFixed(1) + 'K';
      }
      return num.toString();
    },
    
         // 
    season(val) {
      return this.icons[val];
    },
    
    /**
     * 
     */
    changeDataset() {
      // 
      const supportedVisualizations = this.getDatasetVisualizationOptions(this.selectedDataset);
      this.visualList = supportedVisualizations;
      
      // 
      if (supportedVisualizations.length > 0) {
        const currentDatasetOption = this.datasetOptions.find(opt => opt.value === this.selectedDataset);
        const defaultVis = currentDatasetOption?.config?.defaultVisualization || 'umap';
        
        this.selectVisual = supportedVisualizations.find(v => v.value === defaultVis)?.value || 
                           supportedVisualizations[0].value;
      }
      
      console.log(':', {
        dataset: this.selectedDataset,
        supportedVisualizations: supportedVisualizations.map(v => v.value),
        selectedVisualization: this.selectVisual
      });
    },
    
    /**
     * 
     */
    changeAddDataset() {
      // 
      const supportedVisualizations = this.getDatasetVisualizationOptions(this.newSelectDataset);
      this.newVisualList = supportedVisualizations;
      
      // 
      if (supportedVisualizations.length > 0) {
        const newDatasetOption = this.datasetOptions.find(opt => opt.value === this.newSelectDataset);
        const defaultVis = newDatasetOption?.config?.defaultVisualization || 'umap';
        
        this.newSelectVisual = supportedVisualizations.find(v => v.value === defaultVis)?.value || 
                               supportedVisualizations[0].value;
      }
      
      // 
      this.allVisualization.unshift({
        dataset: this.newSelectDataset,
        visual: this.newSelectVisual
      });
      
      // 
      this.showAddVis = false;
      this.newSelectVisual = '';
      this.newSelectDataset = '';
      
      this.$emit('changeLayout', this.allVisualization);
    },
    
    openEdit() {
      this.showEditClass = true;
      this.showAddVis = false;
  },
    
    openAdd() {
      this.showAddVis = true;
      this.showEditClass = false;
  }
  }
};
</script>

