<template>
 <div class="home">
    <!--  -->
    <div class="sidebar" :style="{ width: sidebarWidth + 'px' }">
      <v-tabs
        vertical
        v-model="tab"
        class="sidebar-tabs"
      >
        <div class="tab-buttons">
          <v-tab v-for="item in tabList" :key="item.text" class="tab-button">
            <v-icon>{{ item.icon }}</v-icon>
          </v-tab>
        </div>
        <div class="sidebar-actions">
          <span class="action-icon"><v-icon>mdi-redo</v-icon></span>
          <span class="action-icon"><v-icon>mdi-undo</v-icon></span>
          <hr class="action-divider" />
          <span class="action-icon"><v-icon>mdi-launch</v-icon></span>
        </div>
      </v-tabs>
      
      <!--  -->
      <v-tabs-items v-model="tab" reverse class="sidebar-content">
        <v-tab-item v-for="item in tabList" :key="item.text">
          <v-card color="basil" flat class="full-height">
            <component
              v-bind:is="item.component"
              @getType="getType"
              @getHover="getHover"
              @getGeneExp="getGeneExp"
              @getColorBy="getColorBy"
              @changeLayout="changeLayout"
              @currentRangeSelected="getRangeSelected"
              @panelStateChange="handlePanelStateChange"
              @dataset-changed="handleDatasetChanged"
            ></component>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
      
      <!--  -->
      <div class="resize-handle" v-if="!isPanelOpen" @mousedown="initResize"></div>
    </div>
    
    <!--  -->
    <div class="visualization-area" :class="gridClass" :key="flag">
      <SingleCellGraphVue 
        v-for="(item, i) in datasetList" 
        :key="datasetList[i]"
        class="grid-item"
        :summaryData="summaryData" 
        :isDelete="signDelete" 
        :currentIndex="i" 
        :classInfo="classInfo" 
        :datasetType="datasetList[i]" 
        @deleteGrap="deleteGrap"
      />
    </div>
  </div>
</template>

<script>
import ViewManager from "./tab/ViewManager.vue";
import CellProperties from "./tab/CellProperties.vue";
import Genes from "./tab/Genes.vue";
import SingleCellGraphVue from "./SingleCellGraph.vue";
import _ from "lodash";
import { mapState, mapGetters, mapActions } from 'vuex';
import { nonReactiveDataMixin } from '@/utils/nonReactiveDataStore';

export default {
  name: "HomeView",
  mixins: [nonReactiveDataMixin], //  mixin
  components: { 
    ViewManager, 
    CellProperties, 
    Genes, 
    SingleCellGraphVue
  },
  
  data() {
    return {
      tab: 1,
      tabList: [
        {
          text: "View Manager",
          component: "ViewManager",
          icon: "mdi-hub",
        },
        {
          text: "Cell Properties",
          component: "CellProperties",
          icon: "mdi-cog",
        },
        {
          text: "Genes",
          component: "Genes",
          icon: "mdi-magnify",
        },
      ],
      notCellType: ['x', 'y', 'Siletti_modf_SCANVI_L1', 'Siletti_modf_SCANVI_L2', 'region', 'subregion'],
      addCellType: ['Class'],
      classInfo: {},
      flag: 'a',
      signDelete: false,
      summaryData: {},
      sidebarWidth: 400, // 
      minSidebarWidth: 400, // 
      maxSidebarWidth: 600, // 
      initialX: 0,
      initialWidth: 0,
      isPanelOpen: false // false
    };
  },
  
  async mounted() {
    await this.loadInitialData();
  },
  
  computed: {
    //  computedgettersnonReactiveDataStore
    ...mapState('cellData', [
      // 'cellInfoList', // nonReactiveDataStore
      // 'geneExp',      // nonReactiveDataStore  
      // 'fieldValue',   // nonReactiveDataStore
      'typeInfo',
      'hoverInfo',
      'colorField',
      'datasetList',
      'field',
      'total',
      'dataRange',
      'geneList',
      'datasetName',
      'geneExpRange'
    ]),
    ...mapGetters('cellData', [
      'getCellInfoList',
      'getFieldValue', 
      'getGeneExp'
    ]),
    ...mapGetters('uiState', [
      'getGridClass'
    ]),
    
    // gettersnonReactiveDataStore
    cellInfoList() {
      return this.getCellInfoList;
    },
    fieldValue() {
      return this.getFieldValue;
    },
    geneExp() {
      return this.getGeneExp;
    },
    
    gridClass() {
      return this.getGridClass || '';
    }
  },
  
  watch: {
    datasetList: {
      async handler(val) {
        if (val.includes('multiple')) {
          await this.loadDatasetSummary();
        }
      }
    }
  },
  
  methods: {
    ...mapActions('cellData', [
      'loadFieldsInfo',
      'loadCellTypeValues',
      'setTypeInfo',
      'setHoverInfo',
      'setColorField',
      'loadGeneList'
    ]),
    ...mapActions('uiState', [
      'updateGridClass',
      'setCurrentRangeSelected'
    ]),
    /**
     * 
     */
    async loadInitialData() {
      try {
        // Vuex action
        await this.loadFieldsInfo();
        
        // cell_type_values.json
        await this.loadCellTypeValues();
        
        // 
        await this.$store.dispatch('cellData/loadGeneList');
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     */
    async loadDatasetSummary() {
      try {
        const response = await fetch('/multiple_outputs/dataset_summary.json');
        this.summaryData = await response.json();
      } catch (error) {
        console.error(':', error);
      }
    },
    
    /**
     * 
     */
    getType(val) {
      console.log(' HomeView.getType :', val);
      
      const newTypeInfo = { ...this.typeInfo };
      
      if (val.type === 'plain') {
        console.log(' ');
        newTypeInfo[val.data.key] = val.data.value;
        if (!val.data.value.length) {
          delete newTypeInfo[val.data.key];
        }
      } else {
        console.log('  (Class)');
        newTypeInfo[val.data.key] = val.data.value;
        if (_.isEmpty(newTypeInfo[val.data.key])) {
          delete newTypeInfo[val.data.key];
        }
      }
      
      console.log('  typeInfo:', newTypeInfo);
      this.setTypeInfo(newTypeInfo);
    },
    
    /**
     * 
     */
    getHover(val) {
      let hoverInfo = {};
      
      if (val.mode === 'over') {
        const key = val.type === 'treeView' 
          ? (val.data.hasOwnProperty('children') ? 'Siletti_modf_SCANVI_L1' : 'Siletti_modf_SCANVI_L2') 
          : val.data.key;
          
        const value = val.type === 'treeView' ? val.data.name : val.data.value;
        
        hoverInfo = { key, value, type: val.type };
        if (val.type === 'treeView' && !val.data.hasOwnProperty('children')) {
          hoverInfo['parent'] = val.data.parent;
        }
      }
      
      this.setHoverInfo(hoverInfo);
    },
    
    /**
     * 
     */
    getGeneExp(val) {
      if (_.isEmpty(val)) {
        this.$store.commit('cellData/SET_GENE_EXP', {});
      } else {
        const keys = Object.keys(val);
        const key = keys[keys.length - 1];
        this.$store.commit('cellData/SET_GENE_EXP', { [key]: val[key] });
      }
    },
    
    /**
     * 
     */
    getColorBy(colorBy) {
      this.setColorField(colorBy);
      this.$store.commit('cellData/SET_GENE_EXP_RANGE', { ...this.geneExpRange, color: false });
    },
    
    /**
     * 
     */
    changeLayout(val) {
      this.$store.commit('cellData/SET_DATASET_LIST', val);
      this.updateGridClass();
      this.flag = new Date().getTime();
      this.signDelete = true;
    },
    
    /**
     * 
     */
    deleteGrap(index) {
      console.log(`: ${index}`);
      
      try {
        // 
        const chartComponents = this.$children.filter(child => 
          child.$options.name === 'SingleCellGraphVue'
        );
        
        if (chartComponents[index] && chartComponents[index].$refs.singleChart) {
          const chartRef = chartComponents[index].$refs.singleChart;
          if (chartRef.renderer && typeof chartRef.renderer.dispose === 'function') {
            console.log(`${index}`);
            chartRef.renderer.dispose();
            chartRef.renderer = null;
          }
        }
        
        // 
        this.$nextTick(() => {
          setTimeout(() => {
            const newDatasetList = [...this.datasetList];
            newDatasetList.splice(index, 1);
            this.$store.commit('cellData/SET_DATASET_LIST', newDatasetList);
            this.updateGridClass();
            this.flag = new Date().getTime();
            this.signDelete = newDatasetList.length > 1;
            
            console.log(`: ${newDatasetList.length}`);
          }, 100); // 100msDOM
        });
      } catch (error) {
        console.error(':', error);
        
        // 
        const newDatasetList = [...this.datasetList];
        newDatasetList.splice(index, 1);
        this.$store.commit('cellData/SET_DATASET_LIST', newDatasetList);
        this.updateGridClass();
        this.flag = new Date().getTime();
        this.signDelete = newDatasetList.length > 1;
      }
    },
    
    /**
     * 
     */
    getRangeSelected(val) {
      this.$store.commit('cellData/SET_GENE_EXP_RANGE', val);
      this.setCurrentRangeSelected(val);
    },
    
    /**
     * 
     */
    initResize(e) {
      e.preventDefault();
      document.addEventListener('mousemove', this.doResize);
      document.addEventListener('mouseup', this.stopResize);
      // body
      document.body.style.userSelect = 'none';
      this.initialX = e.clientX;
      this.initialWidth = this.sidebarWidth;
    },
    
    /**
     * 
     */
    doResize(e) {
      this.sidebarWidth = this.initialWidth + (e.clientX - this.initialX);
      // 
      if (this.sidebarWidth < this.minSidebarWidth) this.sidebarWidth = this.minSidebarWidth;
      if (this.sidebarWidth > this.maxSidebarWidth) this.sidebarWidth = this.maxSidebarWidth;
    },
    
    /**
     * 
     */
    stopResize() {
      document.removeEventListener('mousemove', this.doResize);
      document.removeEventListener('mouseup', this.stopResize);
      // 
      document.body.style.userSelect = '';
    },
    
    handlePanelStateChange(state) {
      this.isPanelOpen = state;
    },
    
    /**
     * 
     */
    async handleDatasetChanged(event) {
      try {
        console.log('HomeView: ', event);
        
        // 
        await this.loadInitialData();
        
        // 
        this.flag = this.flag === 'a' ? 'b' : 'a';
        
        console.log('HomeView: ');
      } catch (error) {
        console.error('HomeView: :', error);
      }
    }
  },
  
  // 
  beforeDestroy() {
    document.removeEventListener('mousemove', this.doResize);
    document.removeEventListener('mouseup', this.stopResize);
  },
};
</script>

