<template>
  <div @mouseenter="onComponentEnter" @mouseleave="onComponentLeave">
    <v-virtual-scroll :height="300" :items="typeList" item-height="48">
      <template v-slot:default="{ item }">
        <div 
          :title="item.name" 
          style="display:flex;justify-content:space-between;padding:5px 0px 0px 10px;" 
          @mouseenter="onMouseEnter(item)" 
          @mouseleave="onMouseLeave(item)"
          class="type-item"
        >
          <v-checkbox
            v-model="checkList"
            hide-details
            :label="item.name"
            :value="item.id"
            class="shrink mt-0 long-label-checkbox"
            @change="getCheckList"
          ></v-checkbox>
          <div class="check-info shrink">
              <div class="color-bar" v-if="currentColorBy===typeName">
              <div
                class="color-item"
                :style="{ backgroundColor: getColor(item.name), width: '100%' }"
              ></div>
            </div>
            <div class="color-bar" v-else>
              <div
                class="color-item"
                v-for="(color, i) in colors"
                :key="i"
                :style="{ backgroundColor: color, width: '20%' }"
              ></div>
            </div>
            <span style="font-size: 14px">2.05M</span>
          </div>
        </div>
      </template>
    </v-virtual-scroll>
    <!-- <v-row align="center" v-for="(item,index) in classes" :key="index" justify="space-between" style="min-height:2.5rem;" class="type-item" @mouseover="onOver(item)" @mouseleave="onLeave(item)">
        <v-checkbox
          v-model="checkList"
          hide-details
          :label="item.label"
          :value="item.value"
          class="shrink  mt-0  long-label-checkbox"
          @change="getCheckList"
        ></v-checkbox>
        <div class="check-info shrink">
            <div class="color-bar">
                <div class="color-item" v-for="(color ,i) in colors " :key="i" :style="{backgroundColor:color,width:'20%'}"></div>
            </div>
            <span style="font-size:14px;">2.05M</span>
        </div>
    </v-row> -->
  </div>
</template>

<script>
import _ from "lodash";
import { getColorFromValue } from '@/utils/tool';
import { mapState } from 'vuex';

export default {
  data() {
    return {
      checkList: [],
      colors: [],
      classes: [],
      highlightTimeout: null,
      componentHovered: false
    };
  },
  props: {
    typeList: {
      type: Array,
      default: () => [],
    },
    typeName: {
      type: String,
      default: () => "",
    },
    currentColorBy:{
      type:String,
      default:'Class'
    }
  },
  computed: {
    ...mapState('cellData', [
      'fieldValue',
      'field'
    ])
  },
  watch:{
    currentColorBy:{
      handler(val){
        console.log(val,83);
        this.colors = []; // 
        let key=this.currentColorBy==='Class'?'Siletti_modf_SCANVI_L1':this.currentColorBy;
        let typeIndex=this.field.indexOf(key);
        let items=this.fieldValue[key];
      
        for(let i=0;i<10;i++){
          let color=getColorFromValue(i,typeIndex);
          this.colors.push(`rgba(${color[0] * 255}, ${color[1] * 255}, ${
            color[2] * 255
          }, ${color[3]})`);
        }
      },
      immediate:true
    },
  },
  mounted() {
    this.classes = this.typeList.map((item) => {
      return {
        label: item.id,
        value: item.id,
      };
    });
  },
  methods: {
    getCheckList() {
      console.log(`===  ===`);
      console.log(`: ${this.typeName}`);
      console.log(`: ${this.checkList.length}`);
      console.log(`: [${this.checkList.join(', ')}]`);
      
      this.$emit("getCheckList", {
        key: this.typeName,
        value: this.checkList,
        mode: "check",
      });
      
      console.log(`: ${this.typeName}, : ${this.checkList.length}, : [${this.checkList.join(', ')}]`);
      
      // 
      if (this.checkList.length > 0) {
        // 
        const valueIndices = [];
        
        //  
        for (const checkItem of this.checkList) {
          const selectedItem = this.typeList.find(item => item.id === checkItem);
          if (selectedItem) {
            //  
            // fieldValue
            let valueIndex = -1;
            if (this.fieldValue && this.fieldValue[this.typeName] && Array.isArray(this.fieldValue[this.typeName])) {
              valueIndex = this.fieldValue[this.typeName].indexOf(selectedItem.name);
              if (valueIndex >= 0) {
                valueIndices.push(valueIndex);
                console.log(`: ${selectedItem.name}, : ${valueIndex}`);
              }
            }
          }
        }
        
        console.log(`: [${valueIndices.join(', ')}]`);
        
        if (valueIndices.length > 0) {
          // 
          const event = new CustomEvent('highlight-multi-attribute-intersection', {
            bubbles: true,
            detail: {
              attributeName: this.typeName,
              attributeValueIndices: valueIndices
            }
          });
          this.$el.dispatchEvent(event);
          
          console.log(`: ${this.typeName}, : [${valueIndices.join(', ')}]`);
        }
      } else {
        // 
        console.log(`${this.typeName}`);
        const clearEvent = new CustomEvent('highlight-multi-attribute-intersection', {
          bubbles: true,
          detail: {
            attributeName: this.typeName,
            attributeValueIndices: [] // 
          }
        });
        this.$el.dispatchEvent(clearEvent);
      }
      
      console.log(`===  ===`);
    },
    
    // 
    onComponentEnter() {
      console.log('');
      this.componentHovered = true;
      
      // 
      const event = new CustomEvent('mouse-enter-list-area', {
        bubbles: true
      });
      this.$el.dispatchEvent(event);
      
      // 
      if (this.checkList.length > 0) {
        console.log(`${this.checkList.length}`);
        // 
        this.getCheckList();
      }
    },
    
    // 
    onComponentLeave() {
      console.log('');
      this.componentHovered = false;
      
      // 
      setTimeout(() => {
        // 
        if (!this.componentHovered) {
          // 
          const event = new CustomEvent('mouse-leave-list-area', {
            bubbles: true
          });
          this.$el.dispatchEvent(event);
          
          // 
          const clearHoverEvent = new CustomEvent('cell-property-item-mouseleave', {
            bubbles: true
          });
          this.$el.dispatchEvent(clearHoverEvent);
          
          //  clear-highlight 
          // 
          if (this.checkList.length === 0) {
            console.log('');
            const clearHighlightEvent = new CustomEvent('clear-highlight', {
              bubbles: true,
              detail: {
                attributeName: this.typeName,
                reason: 'no-checked-items'
              }
            });
            this.$el.dispatchEvent(clearHighlightEvent);
          } else {
            console.log(`${this.checkList.length}`);
          }
          
          console.log('');
        }
      }, 100); // 100ms
    },
    
    // 
    onMouseEnter(item) {
      // 
      if (this.highlightTimeout) {
        clearTimeout(this.highlightTimeout);
      }
      
      //  
      // fieldValue
      let valueIndex = -1;
      if (this.fieldValue && this.fieldValue[this.typeName] && Array.isArray(this.fieldValue[this.typeName])) {
        valueIndex = this.fieldValue[this.typeName].indexOf(item.name);
      }

      // 
      console.log(`: ${this.typeName} - ${item.name}, : ${valueIndex}`);
      
      // 
      this.$emit("getHoverItem", {
        key: this.typeName,
        value: item.id,
        mode: "over",
        valueIndex: valueIndex // 
      });
      
      // 
      if (valueIndex >= 0) {
        // 
        const event = new CustomEvent('cell-property-item-mouseenter', {
          bubbles: true,
          detail: {
            attributeName: this.typeName,
            attributeValue: item.name,
            valueIndex: valueIndex
          }
        });
        
        // 
        this.$el.dispatchEvent(event);
      } else {
        console.warn(` ${this.typeName}  ${item.name} `);
      }
    },
    
    // 
    onMouseLeave(item) {
      // 
      if (this.highlightTimeout) {
        clearTimeout(this.highlightTimeout);
      }
      
      // 
      this.$emit("getHoverItem", {
        key: this.typeName,
        value: item.id,
        mode: "leave",
      });
      
      // 
      // onComponentLeave
      console.log('', this.checkList.length);
    },
    
    // onOver
    onOver: _.debounce(function(item) {
      this.$emit("getHoverItem", {
        key: this.typeName,
        value: item.value,
        mode: "over",
      });
    }),
    
    // onLeave
    onLeave: _.debounce(function(item) {
      this.$emit("getHoverItem", {
        key: this.typeName,
        value: item.value,
        mode: "leave",
      });
    }),
    
    getColor(label){
      console.log(this.field,this.fieldValue,label,137);
      //  let key=this.currentColorBy==='Class'?'Siletti_modf_SCANVI_L1':this.currentColorBy
      let typeIndex=this.field.indexOf(this.typeName);
     
      let typeItemIndex=this.fieldValue[this.typeName].indexOf(label);
      let color=getColorFromValue(typeItemIndex,typeIndex);
      console.log(color,344,typeItemIndex,typeIndex);
      return  `rgba(${color[0] * 255}, ${color[1] * 255}, ${
          color[2] * 255
        }, ${color[3]})`;
    },
    
    clearAllHighlights() {
      console.log('');
      
      // 
      if (this.highlightTimeout) {
        clearTimeout(this.highlightTimeout);
        this.highlightTimeout = null;
      }
      
      // 
      const clearEvent = new CustomEvent('cell-property-item-mouseleave', {
        bubbles: true
      });
      
      // 
      this.$el.dispatchEvent(clearEvent);
      
      // 
      if (this.checkList.length === 0) {
        console.log('');
        const clearHighlightEvent = new CustomEvent('clear-highlight', {
          bubbles: true,
          detail: {
            attributeName: this.typeName,
            reason: 'component-destroy'
          }
        });
        this.$el.dispatchEvent(clearHighlightEvent);
      } else {
        console.log(`${this.checkList.length}`);
        //  clear-highlight 
      }
    },
  },
  beforeDestroy() {
    // 
    this.clearAllHighlights();
    
    // 
    if (this.highlightTimeout) {
      clearTimeout(this.highlightTimeout);
      this.highlightTimeout = null;
    }
  },
};
</script>

<style scoped lang="scss">
.check-info {
  display: flex;
}
.color-bar {
  margin-right: 10px;
  display: flex;
  height: 15px;
  width: 6.25rem;
}
.type-item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  padding: 0 5px;
  white-space: nowrap;
  flex-wrap: nowrap;

  &:hover {
    background-color: #ebebeb;
  }
}
.long-label-checkbox .v-label {
  width: 30%;
  white-space: nowrap; /*  */
  overflow: hidden; /*  */
  text-overflow: ellipsis; /*  */
  max-width: 200px; /*  */
}
:deep .v-input--selection-controls .v-input__slot > .v-label {
  height: auto;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 80px;
}
</style>
