<template>
  <div class="tab-panel">
    <div class="vm-header br cell-row1">
      <p>
        <span class="color-block"></span>
        <span class="cell-row1-text">SELECTED VIEW</span>
      </p>
      <p class="dataset-title">
        {{datasetName}}
        <v-icon dense class="icon-set small-icon"
          >mdi-information-outline</v-icon
        >
      </p>
      <p>{{datasetName}}</p>
    </div>
    <div
      class="vm-header br cell-row1 cell-row2"
    >
      <p class="properties-title">
        <span>Cell Properties</span>
        <v-icon class="icon-set small-icon" dense>mdi-cog</v-icon>
      </p>
      <p class="cell-count">{{formatFileSize(total)}} cells</p>
    </div>
    <div class="panel-container" style="position: relative;">
     <v-overlay
      :value="!cellInfoList.length"
      absolute
      opacity="0.1"
    >
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
      <v-expansion-panels class="expansion-panels" accordion>
        <v-expansion-panel v-for="(item, i) in cellInfoList" :key="i">
          <v-expansion-panel-header
            expand-icon=""
            class="panel-header"
          >
            <template v-slot:default="{ open }">
              <v-row no-gutters class="panel-row">
                <v-col
                  :cols="12"
                  :sm="6"
                  class="panel-col-left"
                >
                  <v-icon v-if="!open">mdi-chevron-right</v-icon>
                  <v-icon v-else>mdi-chevron-down</v-icon> 
                  <span class="item-name">{{ item.name }}</span>
                  <span class="filter-style" v-if="getFilterNum(item.name)"
                    >{{ getFilterNum(item.name) }} filters</span
                  >
                </v-col>
                <v-col :cols="12" :sm="6" class="panel-col-right">
                  <v-fade-transition leave-absolute>
                    <span
                      class="action-icons"
                      key="0"
                    >
                      <v-icon v-if="open" class="icon-set">mdi-plus</v-icon>
                      <v-icon v-if="open" class="icon-set">mdi-minus</v-icon>
                      <v-icon v-if="open" class="icon-set">mdi-magnify</v-icon>
                      <span
                        @click.stop="changeByColor(item.name)"
                        class="icon-set icon10"
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          enable-background="new 0 0 24 24"
                          height="24px"
                          viewBox="0 0 24 24"
                          width="24px"
                          fill="#5f6368"
                          class="v-icon__svg"
                          :style="{'fill':currentColorBy===item.name?'#297aba':'#6c6c6c'}"
                        >
                          <rect fill="none" height="24" width="24" />
                          <path
                            d="M12,2c-5.33,4.55-8,8.48-8,11.8c0,4.98,3.8,8.2,8,8.2s8-3.22,8-8.2C20,10.48,17.33,6.55,12,2z M12,20c-3.35,0-6-2.57-6-6.2 c0-2.34,1.95-5.44,6-9.14c4.05,3.7,6,6.79,6,9.14C18,17.43,15.35,20,12,20z M7.83,14c0.37,0,0.67,0.26,0.74,0.62 c0.41,2.22,2.28,2.98,3.64,2.87c0.43-0.02,0.79,0.32,0.79,0.75c0,0.4-0.32,0.73-0.72,0.75c-2.13,0.13-4.62-1.09-5.19-4.12 C7.01,14.42,7.37,14,7.83,14z"
                          />
                        </svg>
                      </span>
                    </span>
                  </v-fade-transition>
                </v-col>
              </v-row>
            </template>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <component
              v-bind:is="currentComponent"
              :typeName="item.name"
              :typeList="item.children"
              :field="field"
              :fieldValue="fieldValue"
              :currentColorBy="currentColorBy"
              @getCheckList="getCheckList"
              @getHoverItem="getHoverItem"
              v-if="item.name !== 'Class'"
              @mouseenter.native="handleAttributeMouseEnter"
              @mouseleave.native="handleAttributeMouseLeave"
            ></component>
            <v-treeview
              v-else
              v-model="treeSelection"
              :items="item.children"
              selected-color="indigo"
              selectable
              hoverable
              expand-icon="mdi-chevron-down"
              @input="getTreeSelected"
              class="tree-view"
            >
              <template v-slot:label="{ item }">
                <div
                  class="tree-item"
                  @mouseover="handleMouseOver(item)"
                  @mouseleave="handleMouseOut(item)"
                >
                 <span class="tree-item-name"> {{ item.name }}</span>
                 <div class="color-list">
                   <span class="color-item" v-if="currentColorBy==='Class'" :style="{backgroundColor:getColor(item.name)}"></span>
                   <div class="color-list-container" v-else>
                     <span class="color-item" v-for="(color,i) in getColorList()" :key='i' :style="{backgroundColor:color}"></span>
                   </div>
                 </div>
                </div>
              </template>
            </v-treeview>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
  </div>
</template>

<script>
import { getColorFromValue } from '@/utils/tool';
import typeListItem from "../components/typeListItem.vue";
import { mapState, mapGetters, mapActions } from 'vuex';
import { nonReactiveDataMixin } from '@/utils/nonReactiveDataStore';
import { buildDatasetPath, getDataset } from '@/api/config';

export default {
  name: "CellProperties",
  mixins: [nonReactiveDataMixin], //  mixin
  components: {
    typeListItem,
  },
  data() {
    return {
      expandPanelList: [],
      items: [],
      currentComponent: "typeListItem",
      treeSelection: [],
      currentColorBy: "Class",
      highlightedAttribute: null,
      highlightedValue: -1,
    };
  },
  computed: {
    //  computedgettersnonReactiveDataStore
    ...mapState('cellData', [
      // 'cellInfoList', // nonReactiveDataStore
      // 'fieldValue',   // nonReactiveDataStore
      'typeInfo',
      'total',
      'field',
      'datasetName'
    ]),
    ...mapGetters('cellData', [
      'getCellInfoList',
      'getFieldValue'
    ]),
    
    // gettersnonReactiveDataStore
    cellInfoList() {
      return this.getCellInfoList;
    },
    fieldValue() {
      return this.getFieldValue;
    }
  },
  watch:{
   field:{
    handler(val){
      this.currentColorBy=val[0]
    }
   }
  },
  mounted() {
    // 
    this.addGlobalEventListeners();
    
    // 
    if (typeof window !== 'undefined') {
      window.addEventListener('datasetChanged', this.handleDatasetChange);
    }
  },
  beforeDestroy() {
    // 
    this.removeGlobalEventListeners();
    
    // 
    if (typeof window !== 'undefined') {
      window.removeEventListener('datasetChanged', this.handleDatasetChange);
    }
  },
  methods: {
    ...mapActions('cellData', [
      'loadFieldsInfo',
      'loadCellTypeValues'
    ]),
    
    /**
     * 
     */
    async handleDatasetChange(event) {
      console.log('CellProperties: ', event.detail.dataset);
      
      try {
        
        // 
        this.currentColorBy = "Class";
        this.highlightedAttribute = null;
        this.highlightedValue = -1;
        this.treeSelection = [];
        
        //  
        await new Promise(resolve => setTimeout(resolve, 100));
        
        // 
        console.log('CellProperties: ...');
        await this.loadDataWithRetry('loadFieldsInfo', 3);
        
        console.log('CellProperties: ...');
        await this.loadDataWithRetry('loadCellTypeValues', 3);
        
        console.log('CellProperties: ');
        
        // 
        this.$emit('dataset-changed', {
          dataset: event.detail.dataset
        });
        
      } catch (error) {
        console.error('CellProperties: :', error);
        // 
        // 
      } 
    },
    
    /**
     * 
     * @param {string} actionName - action
     * @param {number} maxRetries - 
     * @param {number} delay - (ms)
     */
    async loadDataWithRetry(actionName, maxRetries = 3, delay = 200) {
      let lastError = null;
      
      for (let attempt = 1; attempt <= maxRetries; attempt++) {
        try {
          await this[actionName]();
          return; // 
        } catch (error) {
          lastError = error;
          console.warn(`CellProperties: ${actionName} ${attempt}:`, error.message);
          
          if (attempt < maxRetries) {
            // 
            const retryDelay = delay * Math.pow(2, attempt - 1);
            console.log(`CellProperties: ${retryDelay}ms${attempt + 1}...`);
            await new Promise(resolve => setTimeout(resolve, retryDelay));
          }
        }
      }
      
      // 
      console.error(`CellProperties: ${actionName} ${maxRetries}:`, lastError);
      throw lastError;
    },
    getFilterNum(key) {
      if (this.typeInfo.hasOwnProperty(key)) {
        return key === "Class"
          ? Object.keys(this.typeInfo[key]).length
          : this.typeInfo[key].length;
      } else {
        return 0;
      }
    },
    getCheckList(val) {
      this.$emit("getType", { type: "plain", data: val });
    },
    getTreeSelected(val) {
      console.log(val, 197);
      let categoryData = this.categorizedData(val);
      this.$emit("getType", {
        type: "treeView",
        data: { key: "Class", value: categoryData, mode: "selected" },
      });
      
      //  Class
      if (val && val.length > 0) {
        // 
        const valueIndices = [];
        // ClassL2Siletti_modf_SCANVI_L2
        const actualAttributeName = 'Siletti_modf_SCANVI_L2'; 
        
        // 
        for (const selectedItemName of val) {
          // 
          if (this.fieldValue && this.fieldValue[actualAttributeName] && Array.isArray(this.fieldValue[actualAttributeName])) {
            const valueIndex = this.fieldValue[actualAttributeName].indexOf(selectedItemName);
            if (valueIndex >= 0) {
              valueIndices.push(valueIndex);
              console.log(`Class: ${selectedItemName}, : ${valueIndex}`);
            } else {
              console.warn(`Class ${selectedItemName}  ${actualAttributeName} `);
            }
          }
        }
        
        console.log(`Class: [${valueIndices.join(', ')}]`);
        
        if (valueIndices.length > 0) {
          // 
          const event = new CustomEvent('highlight-multiple-attributes', {
            bubbles: true,
            detail: {
              attributeName: actualAttributeName,
              attributeValueIndices: valueIndices,
              isCheckHighlight: true // 
            }
          });
          this.$el.dispatchEvent(event);
          
          console.log(`Class: ${actualAttributeName}, : [${valueIndices.join(', ')}]`);
        }
      } else {
        // Class
        console.log('Class');
        const clearEvent = new CustomEvent('clear-highlight', {
          bubbles: true,
          detail: {
            attributeName: 'Siletti_modf_SCANVI_L2',
            force: true,
            reason: 'class-uncheck'
          }
        });
        this.$el.dispatchEvent(clearEvent);
      }
    },
    categorizedData(treeData = []) {
      const categorizedData = {};

      treeData.forEach((item) => {
        // 
        // const prefix = item.split("-")[0];
        const prefix = item
          .match(/^([A-Za-z0-9\s-]+(?: and [A-Za-z0-9\s-]+)?)-\d+$/)[1]
          .trim();

        // 
        if (!categorizedData[prefix]) {
          categorizedData[prefix] = [];
        }

        // 
        categorizedData[prefix].push(item);
      });
      return categorizedData;
    },
    handleMouseOver(item) {
      console.log(item, 209);
      this.$emit("getHover", { type: "treeView", data: item, mode: "over" });
      
      //  Class
      if (!item.children && item.parent) {
        // Siletti_modf_SCANVI_L2
        this.highlightTreeItem('Siletti_modf_SCANVI_L2', item.name);
      } else {
        // Siletti_modf_SCANVI_L1
        this.highlightTreeItem('Siletti_modf_SCANVI_L1', item.name);
      }
    },
    handleMouseOut(item) {
      console.log(item, 215);
      this.$emit("getHover", { type: "treeView", data: item, mode: "leave" });
      
      // 
      this.clearHighlight();
    },
    getHoverItem(hoverItem) {
      console.log(hoverItem, 246);
      this.$emit("getHover", { type: "plain", data: hoverItem });
      
      // 
      if (hoverItem && hoverItem.key && hoverItem.value !== undefined) {
        this.highlightAttribute(hoverItem.key, hoverItem.value);
      }
    },
    changeByColor(colorKey) {
      this.currentColorBy = colorKey
      let colorField = colorKey === 'Class' ? 'Siletti_modf_SCANVI_L1' : colorKey

      this.$emit("getColorBy", colorField);
    },
    formatFileSize(bytes) {
      if (bytes >= 1024 * 1024 * 1024) {
        return (bytes / (1024 * 1024 * 1024)).toFixed(2) + ' GB';
      }
      if (bytes >= 1024 * 1024) {
        return (bytes / (1024 * 1024)).toFixed(2) + ' MB';
      }
      return bytes
    },
    getColor(label){
      console.log(this.field,this.fieldValue,label,339)
      let key=this.currentColorBy==='Class'?'Siletti_modf_SCANVI_L1':this.currentColorBy
      let typeIndex=this.field.indexOf(key)
     
      let typeItemIndex=this.fieldValue[key].indexOf(label)
      let color=getColorFromValue(typeItemIndex,typeIndex)
      console.log(color,344,typeItemIndex,typeIndex)
      return  `rgba(${color[0] * 255}, ${color[1] * 255}, ${
          color[2] * 255
        }, ${color[3]})`;
    },
    getColorList(){
      let key=this.currentColorBy==='Class'?'Siletti_modf_SCANVI_L1':this.currentColorBy
      let typeIndex=this.field.indexOf(key)
      let items=this.fieldValue[key]
      let colors=[]
      for(let i=0;i<10;i++){
        let color=getColorFromValue(i,typeIndex)
        colors.push(`rgba(${color[0] * 255}, ${color[1] * 255}, ${
        color[2] * 255
      }, ${color[3]})`)
      }
      return colors;
    },
    async loadCellAttribute(attrPath) {
      try {
        console.log(`: ${attrPath}`);
        const response = await fetch(attrPath);
        
        if (!response.ok) {
          console.error(`HTTP ! : ${response.status} ${response.statusText}`);
          throw new Error(`: ${response.status} ${response.statusText}`);
        }
        
        const arrayBuffer = await response.arrayBuffer();
        console.log(`arrayBuffer: ${arrayBuffer.byteLength} `);
        const floatArray = new Float32Array(arrayBuffer);
        console.log(`Float32Array: ${floatArray.length}`);
        
        return floatArray;
      } catch (error) {
        console.error(`:`, error);
        return new Float32Array(0);
      }
    },

    // 
    addGlobalEventListeners() {
      // 
      document.addEventListener('cell-property-item-mouseenter', this.handlePropertyItemMouseEnter);
      document.addEventListener('cell-property-item-mouseleave', this.handlePropertyItemMouseLeave);
    },

    // 
    removeGlobalEventListeners() {
      document.removeEventListener('cell-property-item-mouseenter', this.handlePropertyItemMouseEnter);
      document.removeEventListener('cell-property-item-mouseleave', this.handlePropertyItemMouseLeave);
    },

    // 
    handlePropertyItemMouseEnter(event) {
      if (event.detail) {
        const { attributeName, attributeValue, valueIndex } = event.detail;

        // attributeNamevalueIndex
        if (!attributeName || valueIndex === undefined || valueIndex < 0) {
          console.warn(`: ${attributeName}:${valueIndex}`);
          return;
        }

        console.log(`: ${attributeName}, : ${valueIndex}`);

        //  
        // 
        this.setHoverCategory(attributeName, valueIndex);
      }
    },

    // 
    handlePropertyItemMouseLeave() {
      // 
      this.clearHoverCategory();
    },
    
    //  -  hoverCategory  highlightAttribute
    setHoverCategory(attributeName, valueIndex) {
      //  Vuex store 
      this.$store.dispatch('cellData/setHoverInfo', {
        key: attributeName,
        value: valueIndex,
        type: 'hover'
      });
    },
    
    // 
    clearHoverCategory() {
      //  store
      this.$store.dispatch('cellData/clearHoverInfo');
    },

    //  - 
    highlightAttribute(attributeName, valueIndex) {
      // 
      if (valueIndex < 0) {
        console.warn(`: ${valueIndex}`);
        return;
      }
      
      // Class
      const actualAttributeName = attributeName === 'Class' ? 'Siletti_modf_SCANVI_L1' : attributeName;
      
      // 
      if (!this.field.includes(actualAttributeName)) {
        console.warn(`: ${actualAttributeName}`);
        return;
      }
      
      // 
      this.highlightedAttribute = actualAttributeName;
      this.highlightedValue = valueIndex;
      
      // 
      const highlightEvent = new CustomEvent('highlight-attribute', {
        detail: {
          attributeName: actualAttributeName,
          attributeValue: valueIndex
        },
        bubbles: true
      });
      
      // 
      this.$el.dispatchEvent(highlightEvent);
    },

    // 
    highlightTreeItem(attributeName, itemName) {
      // 
      if (!this.fieldValue || !this.fieldValue[attributeName]) {
        console.warn(`: ${attributeName}`);
        return;
      }
      
      // 
      const attributeValues = this.fieldValue[attributeName];
      if (!attributeValues || !Array.isArray(attributeValues)) {
        console.warn(`: ${attributeName}`);
        return;
      }
      
      const valueIndex = attributeValues.indexOf(itemName);
      if (valueIndex >= 0) {
        this.highlightAttribute(attributeName, valueIndex);
      } else {
        console.warn(` ${itemName}  ${attributeName} `);
      }
    },

    // 
    clearHighlight(force = false) {
      if (!this.highlightedAttribute && !force) return;
      
      // 
      if (!force) {
        // 
        // 
        // 
        console.log('');
        
        // 
        const clearEvent = new CustomEvent('clear-highlight', {
          bubbles: true,
          detail: {
            attributeName: this.highlightedAttribute,
            checkForCheckedItems: true, // 
            reason: 'parent-clear-request'
          }
        });
        
        // 
        this.$el.dispatchEvent(clearEvent);
        return;
      }
      
      // 
      this.highlightedAttribute = null;
      this.highlightedValue = -1;
      
      // 
      const clearEvent = new CustomEvent('clear-highlight', {
        bubbles: true,
        detail: {
          force: true,
          reason: 'force-clear'
        }
      });
      
      // 
      this.$el.dispatchEvent(clearEvent);
    },

    // 
    handleAttributeMouseEnter(event) {
      // 
      // item
    },

    // 
    handleAttributeMouseLeave() {
      // 
      this.clearHoverCategory();
      
      // 
      const event = new CustomEvent('cell-property-item-mouseleave', {
        bubbles: true
      });
      
      // 
      this.$el.dispatchEvent(event);
      
      //  clearHighlight()
      // 
    }
  }
};
</script>

