// src/utils/indexedDB.js
/**
 * IndexDB
 * 
 */
 
const DB_NAME = 'cellVisDB';
const DB_VERSION = 2; //  2
const STORE_NAME = 'dataCache';

/**
 * 
 * @returns {Promise<IDBDatabase>} 
 */
export function openDB() {
  return new Promise((resolve, reject) => {
    //  
    const checkRequest = window.indexedDB.open(DB_NAME);
    
    checkRequest.onsuccess = (event) => {
      const existingDb = event.target.result;
      const currentVersion = existingDb.version;
      existingDb.close();
      
      // 
      const targetVersion = Math.max(currentVersion, DB_VERSION);
      console.log(`IndexedDB: =${currentVersion}, =${targetVersion}`);
      
      const request = window.indexedDB.open(DB_NAME, targetVersion);

      // 
      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        console.log(`IndexedDB:  ${event.oldVersion}  ${event.newVersion}`);
        
        // 
        if (!db.objectStoreNames.contains(STORE_NAME)) {
          console.log(`IndexedDB:  ${STORE_NAME}`);
          db.createObjectStore(STORE_NAME, { autoIncrement: true });
        }
      };

      request.onsuccess = (event) => {
        const db = event.target.result;
        console.log(`IndexedDB: =${db.version}`);
        resolve(db);
      };

      request.onerror = (event) => {
        console.error('IndexedDB: :', event.target.error);
        reject(event.target.error);
      };
    };
    
    checkRequest.onerror = (event) => {
      // 
      console.log('IndexedDB: ');
      const request = window.indexedDB.open(DB_NAME, DB_VERSION);

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains(STORE_NAME)) {
          db.createObjectStore(STORE_NAME, { autoIncrement: true });
        }
      };

      request.onsuccess = (event) => {
        const db = event.target.result;
        resolve(db);
      };

      request.onerror = (event) => {
        console.error('IndexedDB: :', event.target.error);
        reject(event.target.error);
      };
    };
  });
}

/**
 * IndexDB
 * @param {string|number} key - 
 * @param {any} value - 
 * @returns {Promise<number>} ID
 */
export function storeMapData(key, value) {
  return openDB().then(db => {
    return new Promise((resolve, reject) => {
      const transaction = db.transaction(STORE_NAME, 'readwrite');
      const store = transaction.objectStore(STORE_NAME);
      // valuekey
      const request = store.put(value, key);
      
      request.onsuccess = () => resolve(request.result);
      
      request.onerror = (event) => {
        console.error(':', event.target.error);
        reject(event.target.error);
      };
      
      // 
      transaction.oncomplete = () => {
        console.log(` ${key} IndexDB`);
      };
      
      transaction.onerror = (event) => {
        console.error(':', event.target.error);
        reject(event.target.error);
      };
    });
  });
}

/**
 * IndexDB
 * @param {string|number} key - 
 * @returns {Promise<any>} 
 */
export function getMapData(key) {
  return openDB().then(db => {
    return new Promise((resolve, reject) => {
      const transaction = db.transaction(STORE_NAME, 'readonly');
      const store = transaction.objectStore(STORE_NAME);
      const request = store.get(key);
      
      request.onsuccess = () => resolve(request.result);
      
      request.onerror = (event) => {
        console.error(':', event.target.error);
        reject(event.target.error);
      };
    });
  });
}

/**
 * IndexDB
 * @param {string|number} key - 
 * @returns {Promise<void>}
 */
export function deleteMapData(key) {
  return openDB().then(db => {
    return new Promise((resolve, reject) => {
      const transaction = db.transaction(STORE_NAME, 'readwrite');
      const store = transaction.objectStore(STORE_NAME);
      const request = store.delete(key);
      
      request.onsuccess = () => {
        console.log(` ${key} IndexDB`);
        resolve();
      };
      
      request.onerror = (event) => {
        console.error(':', event.target.error);
        reject(event.target.error);
      };
    });
  });
}

/**
 * 
 * @returns {Promise<void>}
 */
export function clearStore() {
  return openDB().then(db => {
    return new Promise((resolve, reject) => {
      const transaction = db.transaction(STORE_NAME, 'readwrite');
      const store = transaction.objectStore(STORE_NAME);
      const request = store.clear();
      
      request.onsuccess = () => {
        console.log('');
        resolve();
      };
      
      request.onerror = (event) => {
        console.error(':', event.target.error);
        reject(event.target.error);
      };
    });
  });
}

/**
 * 
 * @returns {Promise<Array>} 
 */
export function getAllKeys() {
  return openDB().then(db => {
    return new Promise((resolve, reject) => {
      const transaction = db.transaction(STORE_NAME, 'readonly');
      const store = transaction.objectStore(STORE_NAME);
      const request = store.getAllKeys();
      
      request.onsuccess = () => resolve(request.result);
      
      request.onerror = (event) => {
        console.error(':', event.target.error);
        reject(event.target.error);
      };
    });
  });
}

/**
 * IndexedDB
 * @returns {Promise<void>}
 */
export function deleteDatabase() {
  return new Promise((resolve, reject) => {
    console.log('IndexedDB: ...');
    
    const deleteRequest = window.indexedDB.deleteDatabase(DB_NAME);
    
    deleteRequest.onsuccess = () => {
      console.log('IndexedDB: ');
      resolve();
    };
    
    deleteRequest.onerror = (event) => {
      console.error('IndexedDB: :', event.target.error);
      reject(event.target.error);
    };
    
    deleteRequest.onblocked = () => {
      console.warn('IndexedDB: ');
      // 
      setTimeout(() => {
        resolve();
      }, 1000);
    };
  });
}

/**
 * IndexedDB
 * @returns {Promise<void>}
 */
export async function resetDatabase() {
  try {
    await deleteDatabase();
    // 
    const db = await openDB();
    db.close();
    console.log('IndexedDB: ');
  } catch (error) {
    console.error('IndexedDB: :', error);
    throw error;
  }
}

/**
 * 
 * @param {string} datasetName - 
 * @returns {Promise<void>}
 */
export async function clearDatasetCache(datasetName) {
  try {
    console.log(`IndexedDB:  ${datasetName} ...`);
    
    const db = await openDB();
    const transaction = db.transaction(STORE_NAME, 'readwrite');
    const store = transaction.objectStore(STORE_NAME);
    
    // 
    const allKeys = await new Promise((resolve, reject) => {
      const request = store.getAllKeys();
      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
    
    // 
    const datasetKeys = allKeys.filter(key => 
      typeof key === 'string' && key.startsWith(`${datasetName}_`)
    );
    
    console.log(`IndexedDB:  ${datasetKeys.length} `);
    
    // 
    const deletePromises = datasetKeys.map(key => 
      new Promise((resolve, reject) => {
        const deleteRequest = store.delete(key);
        deleteRequest.onsuccess = () => resolve();
        deleteRequest.onerror = () => reject(deleteRequest.error);
      })
    );
    
    await Promise.all(deletePromises);
    
    // 
    await new Promise((resolve, reject) => {
      transaction.oncomplete = () => resolve();
      transaction.onerror = () => reject(transaction.error);
    });
    
    db.close();
    console.log(`IndexedDB:  ${datasetName} `);
    
  } catch (error) {
    console.error(`IndexedDB:  (${datasetName}):`, error);
    throw error;
  }
}
