import { storeMapData, getMapData, resetDatabase } from './indexDB';
import { getDataSource, DataSourceType } from '@/api';
import { perfTracker, perfWatcher } from './performance';
import { buildDatasetPath, getDataset } from '../api/config';

/**
 * 
 * @param {string} baseKey - 
 * @returns {string} - 
 */
function createDatasetCacheKey(baseKey) {
  const currentDataset = getDataset();
  if (!currentDataset) {
    console.warn('');
    return baseKey;
  }
  return `${currentDataset}_${baseKey}`;
}

/**
 * 
 * @param {string} key - 
 * @returns {Promise<Object|null>} - null
 */
export async function checkCache(key) {
  try {
    const data = await getMapData(key);
    return data || null;
  } catch (error) {
    console.error(':', error);
    return null;
  }
}

/**
 * IndexedDB
 * @param {string} key - 
 * @param {Object} data - 
 * @returns {Promise<boolean>} - 
 */
export async function cacheData(key, data) {
  try {
    await storeMapData(key, data);
    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
}

/**
 * Float32Array
 * @param {string} url - URL
 * @param {string} [cacheKey=null] - null
 * @returns {Promise<Float32Array>} - 
 */
export async function loadBinaryFile(url, cacheKey = null) {
  // 
  if (cacheKey) {
    const cachedData = await checkCache(cacheKey);
    if (cachedData) {
      console.log(`: ${cacheKey}`);
      return new Float32Array(cachedData);
    }
  }
  
  try {
    const response = await fetch(url);
    if (!response.ok) {
      throw new Error(`: ${response.status} ${response.statusText}`);
    }
    
    const arrayBuffer = await response.arrayBuffer();
    const data = new Float32Array(arrayBuffer);
    
    // 
    if (cacheKey) {
      // TypedArray
      const dataArray = Array.from(data);
      await cacheData(cacheKey, dataArray);
    }
    
    return data;
  } catch (error) {
    console.error(`: ${url}`, error);
    return new Float32Array(0);
  }
}

/**
 * 
 * @param {string} blockPath - 
 * @returns {Promise<{points: Float32Array, count: number}>} - 
 */
export async function loadCoordinatesBin(blockPath) {
  const baseCacheKey = `coords_${blockPath.split('/').pop()}`;
  const cacheKey = createDatasetCacheKey(baseCacheKey);
  
  try {
    perfTracker.startMark(`loadCoords:${blockPath}`);
    
    const data = await loadBinaryFile(blockPath, cacheKey);
    
    // 
    if (data.length === 0 || data.length % 2 !== 0) {
      throw new Error(`: ${blockPath}`);
    }
    
    const count = data.length / 2; // X,Y
    
    const duration = perfTracker.endMark(`loadCoords:${blockPath}`);
    perfWatcher.checkLoadTime(duration);
    
    return { points: data, count };
  } catch (error) {
    console.error(`: ${blockPath}`, error);
    return { points: new Float32Array(0), count: 0 };
  }
}

/**
 * 
 * @param {string} attrPath - 
 * @returns {Promise<Float32Array>} - 
 */
export async function loadCellAttributeBin(attrPath) {
  const baseCacheKey = `attr_${attrPath.split('/').pop()}`;
  const cacheKey = createDatasetCacheKey(baseCacheKey);
  
  try {
    perfTracker.startMark(`loadAttr:${attrPath}`);
    
    const data = await loadBinaryFile(attrPath, cacheKey);
    
    const duration = perfTracker.endMark(`loadAttr:${attrPath}`);
    perfWatcher.checkLoadTime(duration);
    
    return data;
  } catch (error) {
    console.error(`: ${attrPath}`, error);
    return new Float32Array(0);
  }
}

/**
 * 
 * @param {string} url - URL
 * @param {number} offset - 
 * @param {number} chunkSize - 
 * @returns {Promise<Object>} - 
 */
export async function fetchDataChunk(url, offset, chunkSize) {
  try {
    // 
    const dataSource = getDataSource();
    let response;
    
    if (dataSource === DataSourceType.LOCAL) {
      // Range
      response = await fetch(url, {
        headers: { Range: `bytes=${offset}-${offset + chunkSize - 1}` }
      });
    } else {
      // API
      const apiUrl = url.replace(/^\//, '/api/');
      response = await fetch(`${apiUrl}?offset=${offset}&size=${chunkSize}`);
    }
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    const text = await response.text();
    
    // JSON
    let jsonText = text;
    const lastBrace = text.lastIndexOf('}');
    const lastBracket = text.lastIndexOf(']');
    
    // JSON
    const lastComplete = Math.max(lastBrace, lastBracket);
    if (lastComplete > 0) {
      jsonText = text.substring(0, lastComplete + 1);
    }
    
    try {
      // JSON
      return JSON.parse(`[${jsonText}]`);
    } catch (parseError) {
      console.error('JSON:', parseError);
      return [];
    }
  } catch (error) {
    console.error(':', error);
    return [];
  }
}

/**
 * 
 * @param {string} url - URL
 * @param {number} [chunkSize=1000000] - 
 * @param {string} [cacheKey=null] - null
 * @returns {Promise<Object>} - 
 */
export async function loadChunkedData(url, chunkSize = 1000000, cacheKey = null) {
  if (cacheKey) {
    const cachedData = await checkCache(cacheKey);
    if (cachedData) {
      console.log(`: ${cacheKey}`);
      return cachedData;
    }
  }
  
  let allData = [];
  let offset = 0;
  let hasMore = true;
  let attempts = 0;
  const MAX_ATTEMPTS = 5;
  
  while (hasMore && attempts < MAX_ATTEMPTS) {
    try {
      const chunk = await fetchDataChunk(url, offset, chunkSize);
      
      if (chunk.length === 0) {
        hasMore = false;
      } else {
        allData = [...allData, ...chunk];
        offset += chunkSize;
      }
      
      attempts = 0; // 
    } catch (error) {
      attempts++;
      console.error(` ${attempts}/${MAX_ATTEMPTS}:`, error);
      
      if (attempts >= MAX_ATTEMPTS) {
        console.error('');
        hasMore = false;
      }
      
      // 
      await new Promise(resolve => setTimeout(resolve, 1000));
    }
  }
  
  // 
  if (cacheKey && allData.length > 0) {
    await cacheData(cacheKey, allData);
  }
  
  return allData;
}

/**
 * 
 * @param {string} geneName - 
 * @returns {Promise<Object>} - 
 */
export async function loadGeneExpression(geneName) {
  const baseCacheKey = `gene_${geneName}`;
  const cacheKey = createDatasetCacheKey(baseCacheKey);
  const cachedData = await checkCache(cacheKey);
  
  if (cachedData) {
    return cachedData;
  }
  
  try {
    let response;
    const dataSource = getDataSource();
    
    if (dataSource === DataSourceType.LOCAL) {
      response = await fetch(`/gene_expressions/${geneName}.json`);
    } else {
      response = await fetch(`/api/single-cell/gene-expression/${geneName}`);
    }
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    const data = await response.json();
    
    // 
    await cacheData(cacheKey, data);
    
    return data;
  } catch (error) {
    console.error(`${geneName}:`, error);
    return null;
  }
}

/**
 * WebWorker
 * @param {Array} data - 
 * @param {string} operationType - 
 * @returns {Promise<Object>} - 
 */
export function processDataInWorker(data, operationType) {
  return new Promise((resolve, reject) => {
    // BlobWorker
    const workerCode = `
      self.onmessage = function(e) {
        const { data, operationType } = e.data;
        
        let result;
        switch (operationType) {
          case 'buildQuadTree':
            result = buildQuadTree(data);
            break;
          case 'calculateStatistics':
            result = calculateStatistics(data);
            break;
          default:
            self.postMessage({ error: '' });
            return;
        }
        
        self.postMessage({ result });
      };
      
      // 
      function buildQuadTree(data) {
        // 
        return { message: '' };
      }
      
      // 
      function calculateStatistics(data) {
        // 
        return { message: '' };
      }
    `;
    
    const blob = new Blob([workerCode], { type: 'application/javascript' });
    const worker = new Worker(URL.createObjectURL(blob));
    
    worker.onmessage = function(e) {
      if (e.data.error) {
        reject(new Error(e.data.error));
      } else {
        resolve(e.data.result);
      }
      worker.terminate();
    };
    
    worker.onerror = function(error) {
      reject(error);
      worker.terminate();
    };
    
    worker.postMessage({ data, operationType });
  });
}

