/**
 * 
 */
export class DataLoader {
  constructor() {
    this.maxRetries = 3;
    this.retryDelay = 500; // 
    this.abortController = null;
    this.loadStatus = {
      inProgress: false,
      loaded: 0,
      total: 0,
      errors: []
    };
  }

  /**
   * 
   * @param {string} path - 
   * @returns {Promise<Object>} - 
   */
  async loadQuadTree(path) {
    perfTracker.startMark('loadQuadTree');
    
    try {
      const response = await this.fetchWithRetry(path);
      if (!response.ok) {
        throw new Error(`: ${response.status} ${response.statusText}`);
      }
      
      const data = await response.json();
      
      const duration = perfTracker.endMark('loadQuadTree');
      perfWatcher.checkLoadTime(duration);
      
      return data;
    } catch (error) {
      this.handleError('', error);
      throw error;
    }
  }

  /**
   * 
   * @param {string} blockPath - 
   * @returns {Promise<Array>} - 
   */
  async loadDataBlock(blockPath) {
    perfTracker.startMark(`loadBlock:${blockPath}`);
    
    try {
      const response = await this.fetchWithRetry(blockPath);
      if (!response.ok) {
        throw new Error(`: ${response.status} ${response.statusText}`);
      }
      
      const data = await response.json();
      
      const duration = perfTracker.endMark(`loadBlock:${blockPath}`);
      perfWatcher.checkLoadTime(duration);
      
      return data;
    } catch (error) {
      this.handleError(`: ${blockPath}`, error);
      throw error;
    }
  }

  /**
   * 
   * @param {string} blockId - ID
   * @returns {Promise<{points: Float32Array, count: number}>} - 
   */
  async loadCoordinateBlock(blockId) {
    try {
      // IndexedDB - 
      const baseCacheKey = blockId;
      const cacheKey = createDatasetCacheKey(baseCacheKey);
      let data = await getMapData(cacheKey);
      
      // 
      if (!data || data.length === 0) {
        console.log(` ${blockId} `);
        
        // 
        const blockPath = buildDatasetPath(`blocks/${blockId}.bin`);
        
        try {
          // 
          const { points, count } = await loadCoordinatesBin(blockPath);
          
          if (count > 0) {
            // 
            data = [];
            for (let i = 0; i < points.length; i += 2) {
              data.push([points[i], points[i + 1]]);
            }
            
            // 
            await storeMapData(cacheKey, data);
            console.log(` ${blockId}  ${count} `);
          } else {
            console.warn(` ${blockId} `);
            return { points: new Float32Array(0), count: 0 };
          }
        } catch (fileError) {
          console.error(` ${blockId} :`, fileError);
          return { points: new Float32Array(0), count: 0 };
        }
      }
      
      // Float32Array
      const flatArray = new Float32Array(data.flat());
      const count = data.length;
      
      return { points: flatArray, count };
    } catch (error) {
      //  IndexedDB
      if (error.name === 'VersionError') {
        console.warn(`IndexedDB ${blockId}`);
        try {
          await resetDatabase();
          // 
          return await this.loadCoordinateBlock(blockId);
        } catch (resetError) {
          console.error('IndexedDB:', resetError);
        }
      }
      
      console.error(` ${blockId} :`, error);
      
      // 
      try {
        console.log(` ${blockId}`);
        const blockPath = buildDatasetPath(`blocks/${blockId}.bin`);
        const { points, count } = await loadCoordinatesBin(blockPath);
        
        if (count > 0) {
          const data = [];
          for (let i = 0; i < points.length; i += 2) {
            data.push([points[i], points[i + 1]]);
          }
          const flatArray = new Float32Array(data.flat());
          return { points: flatArray, count: data.length };
        }
      } catch (fallbackError) {
        console.error(`:`, fallbackError);
      }
      
      return { points: new Float32Array(0), count: 0 };
    }
  }

  /**
   * 
   * @param {string} fieldName - 
   * @param {Function} progressCallback - 
   * @returns {Promise<Float32Array>} - 
   */
  async loadCellAttribute(fieldName, progressCallback = null) {
    try {
      const attrPath = buildDatasetPath(`cell_types/${fieldName}.bin`);
      
      // 
      this.loadStatus.inProgress = true;
      this.loadStatus.field = fieldName;
      
      // 
      console.log(`: ${attrPath}`);
      
      // abort
      this.abortController = new AbortController();
      
      // 
      const response = await this.fetchWithRetry(attrPath, {
        signal: this.abortController.signal,
        // 
        cache: 'no-store',
        headers: {
          'Cache-Control': 'no-cache, no-store, must-revalidate',
          'Pragma': 'no-cache'
        }
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      // 
      const contentLength = response.headers.get('Content-Length');
      const total = contentLength ? parseInt(contentLength, 10) : 0;
      
      // 
      const reader = response.body.getReader();
      const chunks = [];
      let received = 0;
      
      // 
      while (true) {
        const { done, value } = await reader.read();
        
        if (done) {
          break;
        }
        
        chunks.push(value);
        received += value.length;
        
        // 
        if (progressCallback && total > 0) {
          progressCallback(Math.min(received / total, 0.99)); // 99%1%
        }
      }
      
      // chunksArrayBuffer
      const dataArray = new Uint8Array(received);
      let position = 0;
      for (const chunk of chunks) {
        dataArray.set(chunk, position);
        position += chunk.length;
      }
      
      // Uint8ArrayFloat32Array
      const dataBuffer = dataArray.buffer;
      const floatArray = new Float32Array(dataBuffer);
      
      console.log(` ${fieldName} : ${floatArray.length}`);
      
      //  - 
      const baseCacheKey = `attr_${fieldName}`;
      const cacheKey = createDatasetCacheKey(baseCacheKey);
      await cacheData(cacheKey, Array.from(floatArray));
      
      // 
      if (progressCallback) {
        progressCallback(1.0);
      }
      
      this.loadStatus.inProgress = false;
      return floatArray;
    } catch (error) {
      this.loadStatus.inProgress = false;
      
      if (error.name === 'AbortError') {
        console.log(` ${fieldName} `);
        return new Float32Array(0);
      }
      
      this.handleError(` ${fieldName} `, error);
      throw error;
    }
  }

  /**
   * 
   * @param {Array<string>} fields - 
   * @param {Function} progressCallback - 
   * @returns {Promise<Map<string, Float32Array>>} - 
   */
  async loadAllCellAttributes(fields, progressCallback) {
    this.loadStatus.inProgress = true;
    this.loadStatus.loaded = 0;
    this.loadStatus.total = fields.length;
    this.loadStatus.errors = [];
    
    const attributesMap = new Map();
    
    try {
      // Promise.allSettled
      const results = await Promise.allSettled(
        fields.map(async (field, index) => {
          try {
            const data = await this.loadCellAttribute(field);
            attributesMap.set(field, data);
            
            this.loadStatus.loaded++;
            if (progressCallback) {
              progressCallback({
                loaded: this.loadStatus.loaded,
                total: this.loadStatus.total,
                progress: this.loadStatus.loaded / this.loadStatus.total,
                field
              });
            }
            
            return { field, success: true };
          } catch (error) {
            this.loadStatus.errors.push({ field, error: error.message });
            return { field, success: false, error: error.message };
          }
        })
      );
      
      // 
      const failures = results.filter(result => result.status === 'rejected' || 
                                      (result.status === 'fulfilled' && !result.value.success));
      
      if (failures.length > 0) {
        console.warn(`${failures.length}`);
      }
      
      return attributesMap;
    } catch (error) {
      this.handleError('', error);
      throw error;
    } finally {
      this.loadStatus.inProgress = false;
    }
  }

  /**
   * 
   */
  cancelLoading() {
    if (this.abortController) {
      this.abortController.abort();
      this.loadStatus.inProgress = false;
      console.log('');
    }
  }

  /**
   * 
   * @returns {Object} - 
   */
  getLoadStatus() {
    return { ...this.loadStatus };
  }

  /**
   * fetch
   * @param {string} url - URL
   * @param {Object} options - fetch
   * @returns {Promise<Response>} - fetch
   */
  async fetchWithRetry(url, options = {}) {
    let lastError;
    
    for (let attempt = 0; attempt <= this.maxRetries; attempt++) {
      try {
        if (this.abortController) {
          options.signal = this.abortController.signal;
        }
        
        return await fetch(url, options);
      } catch (error) {
        lastError = error;
        
        // abort
        if (error.name === 'AbortError' || attempt === this.maxRetries) {
          throw error;
        }
        
        // 
        const delay = this.retryDelay * Math.pow(2, attempt);
        console.warn(`${delay}ms(${attempt + 1}/${this.maxRetries}): ${url}`);
        await new Promise(resolve => setTimeout(resolve, delay));
      }
    }
    
    throw lastError;
  }

  /**
   * 
   * @param {string} context - 
   * @param {Error} error - 
   */
  handleError(context, error) {
    if (error.name === 'AbortError') {
      console.log(`${context}: `);
      return;
    }
    
    console.error(`${context}:`, error);
    
    // 
    this.loadStatus.errors.push({
      context,
      message: error.message,
      timestamp: new Date().toISOString()
    });
  }
}

